import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { pushView } from '../action';
import { IMG_URL, addRemit } from '../model';

export class RemitReview extends Component {
  constructor(props) {
    super(props);
  }

  async onSubmit(e) {
    e.preventDefault();
    const { component, profile, photo, recipient } = this.props.remit;

    const profileFields = [
      'firstname', 'lastname', 'phone1', 'phone2', 'country', 'zip',
      'address', 'subaddress', 'account', 'bank',
    ];
    const recipientFields = [
      'id', 'accountname', 'unit', 'phone1', 'phone2', 'country', 'zip',
      'address', 'bank', 'branch', 'account', 'photoType', 'photoFront', 'photoBack',
    ];

    const info = {
      'recipient-isnew': recipient.isNew,
      'update-persontype': photo.persontype,
      'update-idcard': photo.idcardUrl,
      'update-profile': photo.profileUrl,
      'send-amount': component.sendAmount,
      'send-unit': 'KRW',
      'receive-amount': component.receiveAmount,
      'receive-unit': component.unit,
      'coupon-code' : component.couponCode,
      'coupon-amount' : component.couponAmount,
      'fee' : component.fee,
      'rate' : component.rate,
    };

    profileFields.forEach(field => { info[`update-${ field }`] = profile[field]; });
    recipientFields.forEach(field => { info[`recipient-${ field }`] = recipient[field]; });

    const result = await addRemit(info);

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      const newProfile = Object.assign({}, this.props.remit.profile);
      const newPhoto = Object.assign({}, this.props.remit.photo);
      const recipients = this.props.recipients.slice();

      if(recipient.isNew) {
        const newRecipient = Object.assign({}, this.props.remit.recipient);
        const { profile } = this.props;
        newRecipient.user_id = profile.id;

        const lastIndex = recipients.filter(recipient => recipient.user_id === profile.id).length;
        recipients.splice(lastIndex, 0, newRecipient);
      }

      newProfile.persontype = newPhoto.persontype;
      newProfile.idcard = newPhoto.idcardUrl;
      newProfile.profile = newPhoto.profileUrl;
      this.props.dispatch(pushView('remit/03', { profile: newProfile, recipients }));
    }
  }

  onCancel(e) {
    e.preventDefault();
    this.props.dispatch(pushView('remit/01'));
  }

  render() {
    const { component, profile, recipient } = this.props.remit;

    return (
      <div className='remit-review themoin-container'>
        <div style={{marginTop: 30, textAlign: 'center'}}>
          <img src={ `${ IMG_URL }/img-review.svg` } />
          <h2 style={{margin: '20px auto'}} className='color-deepblue'>입력하신 정보가 맞나요?</h2>
          <p>입력한 정보를 한 번 더 확인하시고 <br className='mobile'/>송금 신청을 눌러주세요.</p>
        </div>
        <div className='review-amount' style={{marginTop:'25px'}}>
          <div className='moin-body'>보내는 금액</div>
          <div className='reset-bootstrap'>
            <h3>
              <span className='user-sendlist-send color-deepblue'>{ component.sendAmount }</span>
              <span className='user-sendlist-sendunit'>KRW</span>
            </h3>
            <img src={ `${ IMG_URL }/ic-next-p.png` } className='user-sendlist-ic' />
            <h3>
              <span className='user-sendlist-recieve color-skyblue'>{ component.receiveAmount }</span>
              <span className='user-sendlist-recieveunit'>{ component.unit }</span>
            </h3>
          </div>
        </div>
        <div className='review-sender'>
          <div className='review-icon'>
            <img src={ `${ IMG_URL }/img-remitter.svg` } />
            <p style={{marginTop: 10}} className='color-purple'>보내는 사람</p>
          </div>
          <table style={{lineHeight: '30px'}} className='moin-body'>
            <tbody><tr>
              <td style={{width: 100}}>이름</td>
              <td className='color-deepblue'>{ profile.firstname } { profile.lastname }</td>
            </tr>
            <tr>
              <td>휴대전화 번호</td>
              <td className='color-deepblue'>{ profile.phone1 } { profile.phone2 }</td>
            </tr>
            <tr>
              <td>국가</td>
              <td className='color-deepblue'>{ profile.country }</td>
            </tr>
            <tr>
              <td>우편번호</td>
              <td className='color-deepblue'>{ profile.zip }</td>
            </tr>
            <tr>
              <td>주소</td>
              <td className='color-deepblue'>{ profile.address }</td>
            </tr>
            <tr>
              <td>상세 주소</td>
              <td className='color-deepblue'>{ profile.subaddress }</td>
            </tr>
            <tr>
              <td>환불 계좌</td>
              <td className='color-deepblue'>{ profile.bank } { profile.account }</td>
            </tr>
            </tbody>
          </table>
        </div>
        <div className='review-receiver'>
          <div className='review-icon'>
            <img src={ `${ IMG_URL }/img-recipient.svg` } />
            <p style={{marginTop: 10}} className='color-skyblue'>받는 사람</p>
          </div>
          <table style={{lineHeight: '30px'}} className='moin-body'>
            <tbody><tr>
              <td style={{width: 100}}>이름</td>
              <td className='color-deepblue'>{ recipient.accountname }</td>
            </tr>
            <tr>
              <td>휴대전화 번호</td>
              <td className='color-deepblue'>{ recipient.phone1 } { recipient.phone2 }</td>
            </tr>
            <tr>
              <td>국가</td>
              <td className='color-deepblue'>{ recipient.country }</td>
            </tr>
            <tr>
              <td>우편번호</td>
              <td className='color-deepblue'>{ recipient.zip }</td>
            </tr>
            <tr>
              <td>주소</td>
              <td className='color-deepblue'>{ recipient.address }</td>
            </tr>
            <tr>
              <td>은행</td>
              <td className='color-deepblue'>{ recipient.bank }</td>
            </tr>
            { recipient.branch ? (
              <tr>
                <td>지점</td>
                <td className='color-deepblue'>{ recipient.branch }</td>
              </tr>
            ) : null }
            <tr>
              <td>계좌 번호</td>
              <td className='color-deepblue'>{ recipient.account }</td>
            </tr>
            </tbody>
          </table>
        </div>
        <div style={{marginTop: 20}}>
          <form method='post' onSubmit={ this.onSubmit.bind(this) }>
            <button type='submit' className='moin-button'>네, 맞습니다.</button>
          </form>
        </div>
        <div style={{margin: '35px 0 77px 0', textAlign: 'center'}}>
          <a className='color-deepblue underline'
              onClick={ this.onCancel.bind(this) }>
            아니오, 취소하겠습니다.
          </a>
        </div>
      </div>
    )
  }
}

RemitReview.propTypes = {
  dispatch: PropTypes.func,
  remit: PropTypes.object,
  recipients: PropTypes.array,
  profile: PropTypes.object,
};

RemitReview.mapState = (state) => ({
  remit: state.remit,
  recipients: state.recipients,
  profile: state.profile,
});

export default connect(RemitReview.mapState)(RemitReview);


