export const PUSH_VIEW = 'PUSH_VIEW';
export const POP_VIEW = 'POP_VIEW';
export const LOGIN = 'LOGIN';
export const LOGOUT = 'LOGOUT';
export const DELETE_RECIPIENT = 'DELETE_RECIPIENT';
export const UPDATE_REMIT_COMPONENT = 'UPDATE_REMIT_COMPONENT';
export const UPDATE_REMIT_PROFILE = 'UPDATE_REMIT_PROFILE';
export const UPDATE_REMIT_PHOTO = 'UPDATE_REMIT_PHOTO';
export const UPDATE_REMIT_RECIPIENT = 'UPDATE_REMIT_RECIPIENT';
export const UPDATE_REMIT = 'UPDATE_REMIT';
export const UPDATE_LOGIN_STATUS = 'UPDATE_LOGIN_STATUS';

export function pushView(view, param) {
  return {
    type: PUSH_VIEW,
    view,
    param, // profile, recipientId, recipients, remit
  };
}

export function popView(view) {
  return {
    type: POP_VIEW,
    view,
  };
}

export function login(profile) {
  return {
    type: LOGIN,
    profile,
  };
}

export function logout() {
  return {
    type: LOGOUT,
  };
}

export function deleteRecipient(recipientId) {
  return {
    type: DELETE_RECIPIENT,
    recipientId,
  };
}

export function updateRemitComponent(component) {
  return {
    type: UPDATE_REMIT_COMPONENT,
    component,
  };
}

export function updateRemitProfile(profile) {
  return {
    type: UPDATE_REMIT_PROFILE,
    profile,
  };
}

export function updateRemitPhoto(photo) {
  return {
    type: UPDATE_REMIT_PHOTO,
    photo,
  };
}

export function updateRemitRecipient(recipient) {
  return {
    type: UPDATE_REMIT_RECIPIENT,
    recipient,
  };
}

export function updateLoginStatus(profile) {
  return {
    type: UPDATE_LOGIN_STATUS,
    profile,
  };
}


