import React, { Component } from 'react';
import PropTypes from 'prop-types';
import update from 'react-addons-update';
import { connect } from 'react-redux';
import classnames from 'classnames';
import { getEmptyString } from '../util';
import { pushView } from '../action';
import { IMG_URL, updateMember, changeMemberPassword } from '../model';
import MoinTextInput from './components/MoinTextInput';

const KEYS = [
  'email', 'phone1', 'phone2', 'firstname', 'lastname', 'address', 'subaddress', 'zip', 'country',
  'password', 'newpassword', 'repassword', 'account', 'bank',
];

export class Account extends Component {
  constructor(props) {
    super(props);

    const profile = Object.assign({}, props.profile);
    profile.password = '';

    KEYS.forEach(key => {
      switch (key) {
      case 'phone1':
        profile[key] = '+82 (대한민국)';
        break;
      case 'country':
        profile[key] = '대한민국 (South Korea)';
        break;
      default:
        if (!profile[key]) profile[key] = '';
      }
    });

    const validityMap = KEYS.reduce((map, key) => {
      map[key] = true;
      return map;
    }, {});

    this.state = {
      postCodeHidden: true,
      vToken: 0,
      vAddressToken: 0,
      validityMap,
      profile,
      currentTab: 'info', // info or password
    };
  }

  onPasswordSubmit(e) {
    e.preventDefault();

    this.setState({
      vToken: this.state.vToken + 1,
    }, this.submitPassword.bind(this));
  }

  onSubmit(e) {
    e.preventDefault();

    this.setState({
      vToken: this.state.vToken + 1,
    }, this.submit.bind(this));
  }

  async submitPassword() {
    const { password, newpassword } = this.state.profile;
    const isValid = ['password', 'newpassword'].every(key => this.state.validityMap[key]);

    if (!isValid) {
      window.alert('입력한 비밀번호를 다시 한 번 확인해주세요.');
      return;
    }

    const info = { password, newpassword };
    const result = await changeMemberPassword(info);

    if (result.ret === 'failure') {
      window.alert(result.msg);
      return;
    }

    window.alert('비밀번호 변경 성공!');
    this.props.dispatch(pushView('main'));
  }

  async submit() {
    const { firstname, lastname, phone2, zip, address, subaddress, account, bank } = this.state.profile;
    const isValid = KEYS.every(key => this.state.validityMap[key]);

    if (!isValid) {
      window.alert('입력 내용을 다시 한 번 확인해주세요.');
      return;
    }

    const result = await updateMember({
      firstname, lastname, phone2, zip,
      address, subaddress, account, bank,
    });

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      window.alert('수정되었습니다.');
      const profile = Object.assign({}, this.state.profile);
      this.props.dispatch(pushView('main', { profile }));
    }
  }

  onPostCodeOpen() {
    const oncomplete = data => {
      const state = update(this.state, {
        vAddressToken: { $set: this.state.vAddressToken + 1 },
        postCodeHidden: { $set: true },
        profile: {
          zip: { $set: data.zonecode }, // 5자리 새 우편번호 사용
          address: { $set: data.addressEnglish },
        },
      });

      this.setState(state);
    };

    new daum.Postcode({
      width : '100%',
      height : '100%',
      oncomplete,
    }).embed(document.querySelector('.themoin-account .daum-layer'));

    // iframe을 넣은 element를 보이게 한다.
    this.setState({ postCodeHidden: false });
  }

  onPostCodeClose() {
    // iframe을 넣은 element를 안보이게 한다.
    this.setState({ postCodeHidden: true });
  }

  onTextChange(name, e) {
    const { value, valid } = e;
    const newState = {
      validityMap: { [name]: { $set: valid }},
    };

    if (value !== null) newState.profile = { [name] : { $set: value }};

    this.setState(update(this.state, newState));
  }

  renderDaumPostCode() {
    return (
    <div className={ classnames('daum-container', { hidden: this.state.postCodeHidden }) }
        onClick={ this.onPostCodeClose.bind(this) }>
      <div className='daum-layer'>
        <img id='btnCloseLayer'
            src='https://i1.daumcdn.net/localimg/localimages/07/postcode/320/close.png'
            style={{ cursor: 'pointer', position: 'absolute', zIndex: 1 }}
            onClick={ this.onPostCodeClose.bind(this) }
            alt='닫기 버튼' />
      </div>
    </div>
    );
  }

  onTabChange(tab) {
    this.setState({ currentTab: tab });
  }

  renderTab() {
    const { currentTab } = this.state;
    const { email } = this.state.profile;
    const isInfo = currentTab === 'info';

    return (
    <div className='tab-container'>
    <div className='tab'>
      <a className={ isInfo ? 'active' : '' }
          onClick={ this.onTabChange.bind(this, 'info') }>
        회원 정보
      </a>
      <a className={ isInfo ? '' : 'active' }
          onClick={ this.onTabChange.bind(this, 'password') }>
        비밀번호 변경
      </a>
      <div className='spacer'></div>
      <img src={ `${ IMG_URL }/img-man-s.svg` } />
      <p>{ email }</p>
    </div>
    </div>
    );
  }

  renderPasswordChange() {
    const tabIndex = 0;
    const { profile, vToken } = this.state;
    const { password, newpassword, repassword } = profile;

    return (
    <div>
    <div className='content'>
      <h1>비밀번호 변경 안내</h1>
      <p>
        다른 사이트에서 사용한 적이 없는 비밀번호나 이전에 사용한 적이 없는 비밀번호를 입력해주세요.<br />
        생일이나 전화번호, 연속된 숫자 등의 알기 쉬운 비밀번호도 피해주세요.
      </p>
      <form action='/a/v1/member/update' method='post'
          onSubmit={ this.onPasswordSubmit.bind(this) }>
        <div className='form-row'>
          <MoinTextInput className='password'
            label='현재 비밀번호'
            type='password'
            placeholder='현재 비밀번호를 입력해주세요.'
            value={ password }
            vToken={ vToken }
            postValidators={[ MoinTextInput.PasswordValidator ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'password') }>
          </MoinTextInput>
        </div>
        <div className='form-row'>
          <MoinTextInput className='password'
            label='새 비밀번호'
            type='password'
            placeholder='영문 숫자 조합 6~20자'
            value={ newpassword }
            vToken={ vToken }
            postValidators={[ MoinTextInput.PasswordValidator ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'newpassword') }>
          </MoinTextInput>
        </div>
        <div className='form-row'>
          <MoinTextInput className='repassword'
            label='새 비밀번호 확인'
            type='password'
            placeholder='위 비밀번호와 동일하게 입력해주세요.'
            value={ repassword }
            vToken={ vToken }
            postValidators={[
              value => ({
                pass: newpassword === repassword,
                message: '비밀번호가 일치하지 않습니다.',
              }),
            ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'repassword') }>
          </MoinTextInput>
        </div>
        <div className='form-row submit-row'>
          <div className='submit'>
            <button className='account-send-btn'
                type='submit'
                tabIndex={ tabIndex }>
              확인
            </button>
          </div>
        </div> 
      </form>
    </div>
    </div>
    );
  }

  renderContent(isFormContent = false) {
    const tabIndex = this.props.focusable ? 0 : -1;
    const { profile, vToken, vAddressToken } = this.state;
    const {
      firstname, lastname, phone1, phone2,
      country, zip, address, subaddress,
      account, bank,
    } = profile;

    return (
    <div>
    <div className='content'>
      <form action='/a/v1/member/update' method='post'
          onSubmit={ this.onSubmit.bind(this) }>
        <div className='form-row'>
          <MoinTextInput className='firstname'
            label='이름 (First name)'
            placeholder='Gildong'
            value={ firstname }
            vToken={ vToken }
            tabIndex={ tabIndex }
            validators={[ MoinTextInput.KorEngValidator ]}
            postValidators={[ MoinTextInput.EmptyValidator ]}
            onChange={ this.onTextChange.bind(this, 'firstname') }>
          </MoinTextInput>
          <MoinTextInput className='lastname'
            label='성 (Last name)'
            placeholder='Hong'
            value={ lastname }
            vToken={ vToken }
            tabIndex={ tabIndex }
            validators={[ MoinTextInput.KorEngValidator ]}
            postValidators={[ MoinTextInput.EmptyValidator ]}
            onChange={ this.onTextChange.bind(this, 'lastname') }>
          </MoinTextInput>
        </div>
        <div className='form-row'>
          <div className='phone1'>
            <div className='moin-dropdown'>
              <label>휴대전화 번호</label>
              <a id='update-phone1' className='dropdown-toggle' data-toggle='dropdown'
                  role='button' aria-expanded='false'>
                <span className='moin-dropdown-text'>{phone1}</span>
                <img className='pull-right' src={ `${ IMG_URL }/ic-dropdown-p.png` } />
              </a>
              <ul className='dropdown-menu' role='menu'>
                <li><a>+82 (대한민국)</a></li>
              </ul>
            </div>
          </div>
          <MoinTextInput className='phone2'
            label={ getEmptyString() }
            placeholder='01012345678'
            isNumeric={ true }
            maxLength='11'
            value={ phone2 }
            vToken={ vToken }
            tabIndex={ tabIndex }
            postValidators={[ MoinTextInput.EmptyValidator ]}
            onChange={ this.onTextChange.bind(this, 'phone2') }>
          </MoinTextInput>
        </div>
        <div className='form-row'>
          <div className='moin-dropdown country'>
            <label>국가</label>
            <a id='update-country' className='dropdown-toggle' data-toggle='dropdown'
                role='button' aria-expanded='false'>
              <span className='moin-dropdown-text'>{country}</span>
              <img className='pull-right' src={ `${ IMG_URL}/ic-dropdown-p.png` } />
            </a>
            <ul className='dropdown-menu' role='menu'>
              <li><a>{country}</a></li>
            </ul>
          </div>
        </div>
        <div className='form-row'>
          <MoinTextInput className='zip'
            label='우편번호'
            placeholder='12345'
            value={ zip }
            vToken={ vToken + vAddressToken }
            validators={[ MoinTextInput.KorEngValidator ]}
            isNumeric={ true }
            postValidators={[ MoinTextInput.EmptyValidator ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'zip') }>
          </MoinTextInput>
          <div className='postcode'>
            <button className='btn-zipcode'
                tabIndex={ tabIndex }
                onClick={ this.onPostCodeOpen.bind(this) } type='button'>
              우편번호 검색
            </button>
          </div>
        </div>
        <div className='form-row'>
          <MoinTextInput className='address'
            label='주소'
            placeholder='417, Yeongdong-daero, Gangnam-gu, Seoul, Korea'
            value={ address }
            vToken={ vToken + vAddressToken }
            validators={[ MoinTextInput.KorEngValidator ]}
            postValidators={[ MoinTextInput.EmptyValidator ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'address') }>
          </MoinTextInput>
        </div>
        <div className='form-row'>
          <MoinTextInput className='subaddress'
            label='상세주소'
            placeholder='B2 Floor'
            value={ subaddress }
            vToken={ vToken }
            validators={[ MoinTextInput.KorEngValidator ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'subaddress') }>
          </MoinTextInput>
        </div>
        <div className='form-row'>
          <MoinTextInput className='account'
            label='환불 계좌(본인 명의)'
            placeholder='110342123456'
            value={ account }
            vToken={ vToken }
            validators={[ MoinTextInput.KorEngValidator ]}
            postValidators={[ MoinTextInput.EmptyValidator ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'account') }>
          </MoinTextInput>
          <MoinTextInput className='bank'
            label='은행'
            placeholder='신한은행'
            value={ bank }
            vToken={ vToken }
            postValidators={[ MoinTextInput.EmptyValidator ]}
            tabIndex={ tabIndex }
            onChange={ this.onTextChange.bind(this, 'bank') }>
          </MoinTextInput>
        </div>
        { !isFormContent ? 
          <div className='form-row submit-row'>
            <div className='submit'>
              <button className='account-send-btn'
                  type='submit'
                  tabIndex={ tabIndex }>
                저장
              </button>
            </div>
          </div> : null }
      </form>
    </div>
    {this.renderDaumPostCode()}
    </div>
    );
  }

  render() {
    const { currentTab } = this.state;

    return (
    <div className='themoin-account'>
      { this.renderTab() }
      { currentTab === 'info' ?
        this.renderContent() :
        this.renderPasswordChange()
      }
    </div>
    )
  }
}

Account.defaultProps = {
  focusable: true,
};

Account.propTypes = {
  dispatch: PropTypes.func,
  profile: PropTypes.object,
  focusable: PropTypes.bool,
};

Account.mapState = (state) => ({
  profile: state.profile,
});

export default connect(Account.mapState)(Account);



