const _isevent = false;
let feeInfo;

export function setFeeInfo(_feeInfo) {
  feeInfo = _feeInfo;
}

export function getEmptyString(length = 1) {
  let str = '';
  for (let i = 0; i < length; i++) { str += ' '; }

  return str.replace(/ /g, '\u00a0');
}

export function insertComma(int) {
  return int.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}

//송금 수수료 은행별 계산
export function changeSendAmount(send_unit, receive_unit, send_amount, coupon_amount) {
  const receiveAmounts = [];
  const preview = {
    unit: receive_unit,
    moin: {},
    bank: {},
  };
  let range_fee;
  let preview_exchangerate;
  const isevent = _isevent;

  for (let i in feeInfo[send_unit + receive_unit]) {
    let receive_amount = send_amount,
      floatFee = 0,
      jsonFeeBank = feeInfo[send_unit + receive_unit][i],
      strBank = jsonFeeBank['bank'];
    //통합 수수료
    if (jsonFeeBank['fee'])
      receive_amount *= 1 - jsonFeeBank['fee'];

    //보내는통화 수수료
    if (jsonFeeBank[send_unit])
      receive_amount -= jsonFeeBank[send_unit];

    range_fee = receive_amount;
    //보내는통화 금액별 수수료
    if (jsonFeeBank['range']) {
      let floatStdAmount;
      if (jsonFeeBank['range'][0][0] === send_unit)
        floatStdAmount = send_amount;
      else
        floatStdAmount = send_amount / exchangerates[jsonFeeBank['range'][0][0] + send_unit];
      for (let j = 1; j < jsonFeeBank['range'].length; j++) {
        if (floatStdAmount < jsonFeeBank['range'][j][0] || jsonFeeBank['range'][j][0] === 0) {
          if (jsonFeeBank['range'][0][1] === 'per') {
            if (jsonFeeBank['range'][j][1] * 1 > 1)
              receive_amount -= jsonFeeBank['range'][j][1];
            else
              receive_amount *= 1 - jsonFeeBank['range'][j][1];
          } else
            receive_amount -= jsonFeeBank['range'][j][1];
          break;
        }
      }
    }

    //쿠폰?
    if (jsonFeeBank['id'] === 'moin') {
      receive_amount = receive_amount + coupon_amount;
    }

    range_fee -= receive_amount;
    range_fee = Math.round(range_fee);

    //살때환율 계산 , 없으면 실제 환율료 계산
    if (jsonFeeBank['exchangerate']) {
      receive_amount /= exchangerates[receive_unit + send_unit] * (1 + jsonFeeBank['exchangerate']);
      preview_exchangerate = exchangerates[receive_unit + send_unit] * (1 + jsonFeeBank['exchangerate']);
    } else {
      receive_amount /= exchangerates[receive_unit + send_unit];
      preview_exchangerate = exchangerates[receive_unit + send_unit];
    }

    //받는통화 수수료 계산
    if (jsonFeeBank[receive_unit]) {
      receive_amount -= jsonFeeBank[receive_unit];
    }

    //최종 받는금액 및 수수료 계산
    if (receive_amount < 0)
      receive_amount = 0;
    else
      receive_amount = Math.round(receive_amount);
    floatFee = Math.round(send_amount - (receive_amount * exchangerates[receive_unit + send_unit]));

    if (!isevent && jsonFeeBank['id'] === 'moin-event')
      receive_amount = 0;
    receiveAmounts.push(
      {
        name: strBank,
        id: jsonFeeBank['id'],
        receive_amount: receive_amount,
        fee: floatFee,
      }
    );

    if (jsonFeeBank['id'] === 'moin-verify') {
      preview.moin.id = jsonFeeBank['id'];
      preview.moin['send-amount'] = insertComma(send_amount);
      preview.moin.rate = preview_exchangerate;
      preview.moin.fee = insertComma(range_fee);
      preview.moin.receive = insertComma(receive_amount);
      preview.moin.final_fee = send_amount - range_fee > 0 ? insertComma(send_amount - range_fee) : 0;
    } else if(jsonFeeBank['id'] === 'moin' && !preview.moin.id) {
      preview.moin.id = jsonFeeBank['id'];
      preview.moin['send-amount'] = insertComma(send_amount);
      preview.moin.rate = preview_exchangerate;
      preview.moin.fee = insertComma(range_fee);
      preview.moin.receive = insertComma(receive_amount);
      preview.moin.final_fee = send_amount - range_fee > 0 ? insertComma(send_amount - range_fee) : 0;
    } else if(jsonFeeBank['id'] === 'woori') {
      preview.bank.rate = preview_exchangerate;
      preview.bank.fee = insertComma(range_fee);
      preview.bank.fee1 = insertComma(8000);
      preview.bank.fee2 = insertComma(Math.round(jsonFeeBank[receive_unit]*preview_exchangerate));
      preview.bank.receive = insertComma(receive_amount);
      preview.bank.final_fee = receive_amount > 0 ?
        insertComma(Math.round(receive_amount*exchangerates[receive_unit + send_unit])) : 0;
    }
  }

  //내림차순 정렬
  receiveAmounts.sort(function (a, b) {
      return a.receive_amount < b.receive_amount ? 1 : a.receive_amount > b.receive_amount ? -1 : 0;
  });

  return { receiveAmounts, preview };
}

export function changeReceiveAmount(send_unit, receive_unit, Receive_amount, coupon_amount) {
  let resultAmounts = {};

  //받는금액 기준 moin의 보내는금액 역산
  let moinSendamount;
  let jsonFeeMoin = feeInfo[send_unit+receive_unit][0];
  let jsonFeeMoinEvent = feeInfo[send_unit+receive_unit][1];

  let isevent = _isevent;
  if (!isevent)
    moinSendamount = receiveToSendAmount(send_unit, receive_unit, Receive_amount, jsonFeeMoin);
  else
    moinSendamount = receiveToSendAmount(send_unit, receive_unit, Receive_amount, jsonFeeMoinEvent);

  const { receiveAmounts, preview } = changeSendAmount(send_unit, receive_unit, moinSendamount, coupon_amount);

  for(let i = 0 ; i < receiveAmounts.length ; i++) {
    if(!isevent && receiveAmounts[i].id === 'moin') {
      //최종 받는금액 및 수수료 계산
      let floatFee = Math.round(moinSendamount - (Receive_amount * exchangerates[receive_unit + send_unit]));
      receiveAmounts[i].receive_amount = Receive_amount;
      receiveAmounts[i].fee = floatFee;
      break;
    } else if(isevent && receiveAmounts[i].id === 'moin-event') {
      //최종 받는금액 및 수수료 계산
      let floatFee = Math.round(moinSendamount - (Receive_amount * exchangerates[receive_unit + send_unit]));
      receiveAmounts[i].receive_amount = Receive_amount;
      receiveAmounts[i].fee = floatFee;
      break;
    }
  }

  resultAmounts['moinSendamount'] = moinSendamount;
  resultAmounts['receiveAmounts'] = receiveAmounts;
  return { resultAmounts, preview };
}

export function receiveToSendAmount(send_unit, receive_unit, Receive_amount, jsonFeeMoin) {
  let moinSendamount = Receive_amount;

  //받는통화 수수료 계산
  if(jsonFeeMoin[receive_unit]) {
    moinSendamount += jsonFeeMoin[receive_unit];
  }

  //살때환율 계산 , 없으면 실제 환율료 계산
  if(jsonFeeMoin['exchangerate'])
    moinSendamount *= exchangerates[receive_unit + send_unit] * (1 + jsonFeeMoin['exchangerate']);
  else
    moinSendamount *= exchangerates[receive_unit + send_unit];

  //보내는통화 금액별 수수료
  if(jsonFeeMoin['range']) {
    for (let j = jsonFeeMoin['range'].length-1; j > 0; j--) {
      if(j === 1) {
        if(jsonFeeMoin['range'][0][1] === 'per') {
          if (jsonFeeMoin['range'][j][1] > 1)
            moinSendamount += jsonFeeMoin['range'][j][1];
          else
            moinSendamount /= 1 - jsonFeeMoin['range'][j][1];
        } else
          moinSendamount += jsonFeeMoin['range'][j][1];

        if(moinSendamount <= jsonFeeMoin['range'][j-1][0])
          moinSendamount = jsonFeeMoin['range'][j-1][0];
        break;
      }

      let receiveunitRange = jsonFeeMoin['range'][j-1][0];

      if(jsonFeeMoin['range'][0][1] === 'per') {
        if (jsonFeeMoin['range'][j-1][1] > 1) {
          receiveunitRange -= jsonFeeMoin['range'][j-1][1];
        } else
          receiveunitRange *= 1 - jsonFeeMoin['range'][j-1][1];
      } else
        receiveunitRange -= jsonFeeMoin['range'][j-1][1];
      if(receiveunitRange < moinSendamount) {
        if(jsonFeeMoin['range'][0][1] === 'per') {
          if (jsonFeeMoin['range'][j][1] > 1)
            moinSendamount += jsonFeeMoin['range'][j][1];
          else
            moinSendamount /= 1 - jsonFeeMoin['range'][j][1];
        } else
          moinSendamount += jsonFeeMoin['range'][j][1];

        if(moinSendamount <= jsonFeeMoin['range'][j-1][0])
          moinSendamount = jsonFeeMoin['range'][j-1][0];
        break;
      }
    }
  }

  //보내는통화 수수료
  if(jsonFeeMoin[send_unit])
    moinSendamount += jsonFeeMoin[send_unit];

  //통합 수수료
  if(jsonFeeMoin['fee'])
    moinSendamount /= 1-jsonFeeMoin['fee'];

  return Math.round(moinSendamount);
}



