import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Pending from './Pending';
import Header from './Header';
import Footer from './Footer';
import Landing from './Landing';
import Login from './Login';
import SignUp from './SignUp';
import Main from './Main';
import Account from './Account';
import Recipients from './Recipients';
import RecipientsUpdate from './RecipientsUpdate';
import RemitForm from './RemitForm';
import RemitReview from './RemitReview';
import RemitSuccess from './RemitSuccess';
import Company from './Company';
import Info from './Info';
import Terms from './Terms';
import Privacy from './Privacy';
import FAQ from './FAQ';
import Agency from './Agency';
import EventUnivJPY from './events/EventUnivJPY';
import { History } from '../history';
import { Bridge } from '../bridge';
import { fetchProfile } from '../model';

class App extends Component {
  componentDidMount() {
    fetchProfile();
  }

  componentDidUpdate() {
    window.scroll(0, 0);
  }

  renderViewComponent() {
    switch (this.props.view) {
    case 'pending': return <Pending></Pending>;
    case 'main': return <Main></Main>;
    case 'login': return <Login></Login>;
    case 'signup': return <SignUp></SignUp>;
    case 'account': return <Account></Account>;
    case 'recipients': return <Recipients></Recipients>;
    case 'recipients/update':
    case 'recipients/add': return <RecipientsUpdate></RecipientsUpdate>;
    case 'remit/01':
    case 'remit/update': return <RemitForm></RemitForm>;
    case 'remit/02': return <RemitReview></RemitReview>;
    case 'remit/03': return <RemitSuccess></RemitSuccess>;
    case 'company': return <Company></Company>;
    case 'info': return <Info></Info>;
    case 'terms': return <Terms></Terms>;
    case 'privacy': return <Privacy></Privacy>;
    case 'faq': return <FAQ></FAQ>;
    case 'event/univ/jpy': return <EventUnivJPY></EventUnivJPY>;
    case 'agency/moinacademy': return <Agency></Agency>;
    }

    return <Landing></Landing>;
  }

  renderHeader() {
    switch (this.props.view) {
    case 'event/univ/jpy':
    case 'remit/01':
    case 'remit/update':
    case 'agency/moinacademy':
      return null;
    }

    return <Header></Header>;
  }

  renderFooter() {
    switch (this.props.view) {
    case 'remit/01':
    case 'remit/update':
    case 'agency/moinacademy':
      return null;
    }

    return <Footer></Footer>;
  }

  render() {
    return (
    <div>
      { this.renderHeader() }
      { this.renderViewComponent() }
      { this.renderFooter() }
      <History initState={ this.props.initState } />
      <Bridge />
    </div>
    );
  }
}

App.propTypes = {
  initState: PropTypes.object,
  view: PropTypes.string,
};

const mapState = (state) => ({
  view: state.view,
});

App = connect(mapState)(App);
export default App;



