import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { connect } from 'react-redux';
import { RemitComponent } from './RemitComponent';
import MoinUnitDropdown from './components/MoinUnitDropdown';
import MoinPopupPreview from './components/MoinPopupPreview';
import MoinPopupCoupon from './components/MoinPopupCoupon';
import { IMG_URL, useCoupon } from '../model';

class RemitFormComponent extends RemitComponent {
  constructor(props) {
    super(props);

    this.state.previewPopupHidden = true;
    this.state.couponPopupHidden = true;
  }

  async loadCoupon(coupon) {
    const { unit, sendAmount } = this.props.component;
    const info = {
      'coupon-code': coupon,
      'send-amount': sendAmount,
    };

    const { result, data } = await useCoupon(info);

    if (result.ret === 'failure') {
      alert(result.msg);
    } else {
      alert(coupon + ' 쿠폰이 적용되었습니다.');
      this.onCouponPopupClose();
      this.onAmountChange({
        isSend: true,
        couponCode: coupon,
        couponAmount: data.amount,
        comma: true,
        unit,
      });
    }
  }

  cancelCoupon() {
    const { unit } = this.props.component;

    this.onAmountChange({
      isSend: true,
      couponCode: 'cancel',
      couponAmount: 0,
      comma: true,
      unit,
    });
  }

  onPreviewPopupOpen() { this.setState({ previewPopupHidden: false }); }
  onPreviewPopupClose() { this.setState({ previewPopupHidden: true }); }
  onCouponPopupOpen() { this.setState({ couponPopupHidden: false }); }
  onCouponPopupClose() { this.setState({ couponPopupHidden: true }); }

  render() {
    const { feedbackHidden, feedbackText } = this.state;
    const { unit, sendAmount, receiveAmount, couponCode } = this.props.component;
    const moinEventAmountClassName = classnames(
      'moin-event-amount', {
        hidden: feedbackHidden,
      }
    );

    const tabIndex = this.props.focusable ? 0 : -1;

    return (
      <div className='back-white themoin-remit-component'>
        <MoinPopupPreview
          hidden={this.state.previewPopupHidden}
          closePopup={this.onPreviewPopupClose.bind(this)}
          preview={this.state.preview}
        />
        <MoinPopupCoupon
          hidden={this.state.couponPopupHidden}
          closePopup={this.onCouponPopupClose.bind(this)}
          setCoupon={this.loadCoupon.bind(this)}
        />
        <h1>보내는 금액을 입력해주세요.</h1>
        <div className='form-amount'>
          <div id='moin-event-amount' className={moinEventAmountClassName}
            style={{ textAlign: 'center' }}>
            <p className='color-deepgrey'>
              { feedbackText !== null ? feedbackText : '' }
            </p>
          </div>
          <table className='moin-body' style={{ border: 'solid 1px #dbdbdf', width: '100%' }}>
            <tbody>
              <tr style={{ borderBottom: 'solid 1px #dbdbdf' }}>
                <td>
                  <p>내가 보내는 금액</p>
                  <input id='send-amount'
                    className='moin-amount-input color-deepblue'
                    type='text'
                    tabIndex={tabIndex}
                    onChange={this.onAmountChange.bind(this, { isSend: true, unit })}
                    onBlur={this.onAmountBlur.bind(this, true)}
                    value={sendAmount} />
                </td>
                <td className='dropdown'>
                  <a className='dropdown-toggle' data-toggle='dropdown'
                    tabIndex={tabIndex}
                    role='button' aria-expanded='false'>
                    <div className='pull-left'>
                      <div className='moin-body'>대한민국</div>
                      <div id='send-unit' className='moin-unit-text color-deepblue'>KRW</div>
                    </div>
                    <img src={ `${ IMG_URL }/ic-currency-darkblue.svg` }
                      className='moin-unit-toggle pull-right'>
                    </img>
                    <div className='clearfix'></div>
                  </a>
                  <ul className='dropdown-menu' role='menu'>
                    <li><a>대한민국(KRW)</a></li>
                  </ul>
                </td>
              </tr>
              <tr>
                <td>
                  <p>실제로 받는 금액</p>
                  <input id='receive-amount'
                    className='moin-amount-input color-skyblue'
                    type='text'
                    tabIndex={tabIndex}
                    onChange={this.onAmountChange.bind(this, { isSend: false, unit })}
                    onBlur={this.onAmountBlur.bind(this, false)}
                    value={receiveAmount} />
                </td>
                <td>
                  <MoinUnitDropdown label='통화'
                    onChange={this.onUnitChange.bind(this)}
                    value={unit}
                    items={[
                      { title: '일본', value: 'JPY' },
                      //{ title: '중국', value: 'CNY' },
                    ]}>
                  </MoinUnitDropdown>
                </td>
              </tr>
            </tbody>
          </table>
          <input id='coupon-amount' type='hidden' value='0' />
        </div>
        <div className='form-graph'>
          <div className='index-money-rank'>
            {this.state.indexMoneyRanks.map(item => (
              <div key={item.id} id={item.id}
                className={item.className ? item.className : 'index-money-bank'}>
                <div className='index-money-name'>{item.title}</div>
                <div className='index-money-bar'>
                  <div style={{ width: item.width, height: '100%' }}></div>
                </div>
                <div className='index-money-amount'>{item.amount}</div>
                <div className='index-money-unit'>{unit}</div>
                <div className='clearfix'></div>
              </div>
            ))}
          </div>
          <div className='button' style={{ marginTop: '29px' }}>
            <button className='remit-btn moin-body pull-left' type='button'
              tabIndex={tabIndex}
              onClick={this.onPreviewPopupOpen.bind(this)}>
              상세 보기
          </button>
            {!couponCode ?
              <button id='coupon-btn' className='remit-btn moin-body pull-left' type='button'
                tabIndex={tabIndex}
                onClick={this.onCouponPopupOpen.bind(this)}>
                쿠폰 등록
            </button> :
              <button id='coupon-btn' className='remit-btn moin-body pull-left' type='button'
                tabIndex={tabIndex}
                onClick={this.cancelCoupon.bind(this)}>
                쿠폰 취소
            </button>
            }
          </div>
        </div>
      </div>
    );
  }
}

RemitFormComponent.propTypes = {
  dispatch: PropTypes.func,
  component: PropTypes.object,
  focusable: PropTypes.bool,
};

export default connect(RemitComponent.mapState)(RemitFormComponent);



