import React from 'react';
import MoinPopup from './MoinPopup';

export default class MoinPopupRenewal extends MoinPopup {
  renderPopup() {
    return(
      <div className='renewal-popup'>
        <div className='popup-content'>
          <h1 className='color-skyblue'>서비스 점검 안내</h1>
          <p>
            안녕하세요? 모인 해외송금입니다.<br/>
            <br/>
            모인 송금 서비스가 5월 25일부터 7월 18일까지<br/>
            본격적으로 서비스 업그레이드 기간에 들어갑니다.<br/>
            <br/>
            따라서 한국에서 일본 / 중국으로의 송금 서비스는 잠시 중단됩니다.<br/>
            <br/>
            모인을 이용해주시는 많은 분들께 불편을 드려 죄송한 마음이 크지만<br/>
            소중한 고객분들께 더 나은 서비스를 제공하기 위하여<br/>
            잠시 서비스를 재정비할 시간을 갖기로 하였습니다.<br/>
            <br/>
            고객분들의 너그러운 양해 부탁드리며, <br/>
            조만간 더 나은 모습으로 다시 찾아뵙겠습니다.<br/>
            <br/>
            감사합니다.
          </p>
          <div className='popup-btn' onClick={ this.closePopup }>모인 홈페이지 둘러보기</div>
        </div>
      </div>
    );
  }
}


