import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { connect } from 'react-redux';
import { updateRemitRecipient } from '../action';
import { uploadRemitPhoto } from '../model';

const recipientsPhotoType = [{
  title: '여권',
  subtitle: 'Passport / パスポート',
  count: 1,
}, {
  title: '일본 재류 카드',
  subtitle: 'Residence Card / 在留カード',
  count: 2,
}, {
  title: '개별 번호 카드',
  subtitle: 'Individual Number card / 個別番号カード',
  count: 1,
}, {
  title: '운전면허증',
  subtitle: 'Driver’s License / 運転免許証',
  count: 2,
}, {
  title: '특별 상주 주민 자격증',
  subtitle: 'Special Permanent Resident Certificate / 特別永住者証明書',
  count: 2,
}, {
  title: '일단 송금 신청 후 따로 서류를 보내겠습니다.',
  subtitle: '모인 이메일 : info@themoincom\n모인 카카오톡 : @모인 MOIN',
}];

class RemitFormRecipientsPhoto extends Component {
  constructor(props) {
    super(props);

    const { photoType } = this.props.recipient;

    this.state = {
      photoType,
    };
  }

  async uploadImage(file, type) {
    let data = new FormData();
    data.append('file', file);

    const { response, files } = await uploadRemitPhoto(data);

    if (!response.ok) {
      console.log(response.statusText);
      alert('사진 업로드에 실패하였습니다. 다시 시도해주세요.');
    } else {
      const recipient = Object.assign({}, this.props.recipient);
      recipient[type] = files[0].profileUrl;

      this.props.dispatch(updateRemitRecipient(recipient));
    }
  }

  onImageChange(e, input) {
    let file = e.target.files[0];

    this.uploadImage(file, input);
  }

  onChangePhotoType(photoType) {
    const recipient = Object.assign({}, this.props.recipient);

    if (photoType !== null && photoType < 5) {
      photoType += 1;
    } else if (photoType === 5) {
      photoType = 0;
    }

    recipient.photoType = photoType;
    this.props.dispatch(updateRemitRecipient(recipient));
    this.setState({ photoType });
  }

  getPersonPhoto() {
    const tabIndex = this.props.focusable ? 0 : -1;
    const { photoFront, photoBack } = this.props.recipient;
    const phototype = recipientsPhotoType[this.state.photoType - 1];

    return (
    <div>
      <p className='color-deepblue personPhoto'>
        { `${ phototype.title }${ phototype.count === 2 ? '의 앞면/뒷면 ' : '' }사진을 등록해주세요.` }
      </p>
      <div className='getPhoto'>
        <div>
          <div className='remit-pic'
              style={{ backgroundImage: photoFront ? `url("${ photoFront }")` : '' }}>
            <p>{ phototype.count === 2 ? '앞면사진' : '사진' }</p>
          </div>
          <div className='moin-btn-file'>
            사진 등록하기
            <input
              className='upload-pic'
              type='file'
              accept='image/*'
              tabIndex={ tabIndex }
              onChange={ e => { this.onImageChange(e, 'photoFront'); } }
            />
          </div>
        </div>
        { phototype.count === 2 ? 
        <div>
          <div className='remit-pic'
              style={{ backgroundImage: photoBack ? `url("${ photoBack }")'` : '' }}>
            <p>뒷면사진</p>
          </div>
          <div className='moin-btn-file'>
            사진 등록하기
            <input
              className='upload-pic'
              type='file'
              accept='image/*'
              tabIndex={ tabIndex }
              onChange={ e=> { this.onImageChange(e, 'photoBack'); } }
            />
          </div>
        </div> :
        null
        }
      </div>
    </div>
    );
  }

  getCompanyPhoto() {
    const tabIndex = this.props.focusable ? 0 : -1;
    const { photoFront, photoBack } = this.props.recipient;

    return (
      <div className='getPhoto'>
        <div>
          <p className='remit-subtitle'>
            <span>사진 1</span>
            법인 설립 증명서 (6개월 이내 발급)
          </p>
          <div className='remit-pic'
              style={{ backgroundImage: photoFront ? `url("${ photoFront }")` : '' }}>
            <p>
              등록 가능한 파일 확장자<br/>
              (JPG, PNG, GIF)
            </p>
          </div>
          <div className='moin-btn-file'>
            사진 등록하기
            <input type='file'
              className='upload-pic'
              accept='image/*'
              tabIndex={ tabIndex }
              onChange={ e=> { this.onImageChange(e, 'photoFront'); } }
            />
          </div>
        </div>
        <div>
          <p className='remit-subtitle'>
            <span>사진 2</span>
            법인 명의 통장 사본
          </p>
          <div className='remit-pic'
              style={{ backgroundImage: photoBack ? `url("${ photoBack }")` : '' }}>
            <p>
              등록 가능한 파일 확장자<br/>
              (JPG, PNG, GIF)
            </p>
          </div>
          <div className='moin-btn-file'>
            사진 등록하기
            <input
              type='file'
              className='upload-pic'
              accept='image/*'
              tabIndex={ tabIndex }
              onChange={ e=> { this.onImageChange(e, 'photoBack'); } }
            />
          </div>
        </div>
      </div>
    );
  }

  renderSelectType() {
    return (
      <div className='selectType'>
        <p>아래 중에서 한 가지를 선택해주세요.</p>
        <div className='typeList'>
        { recipientsPhotoType.map((photoType, index) => {
          const className = classnames({
            photoType: true,
            typeSelected : index === 5 && this.state.photoType === 0,
          });

          return (
          <a key={ `select-type-${ index }` }
              className={ className }
              onClick={ this.onChangePhotoType.bind(this, index) }>
            <p className='color-deepblue'>{ photoType.title }</p>
            <p>
              { photoType.subtitle.split('\n').map((line, j) => (
                <span key={ `subtitle-${ index }-${ j }`}>
                  { line }<br/>
                </span>
              ))}
            </p>
          </a>
          );
        })}
        </div>
      </div>
    );
  }

  renderSelecter() {
    switch(this.state.photoType) {
      case 1:
      case 2:
      case 3:
      case 4:
      case 5:
        return this.getPersonPhoto();
      case 6:
        return this.getCompanyPhoto();
      default:
        return this.renderSelectType();
    }
  }

  render() {
    const tabIndex = this.props.focusable ? 0 : -1;

    return (
    <div className='RemitFormRecipientsPhoto'>
      <h3>
        받는 분의 증명 서류를 등록해주세요
      </h3>
      <p>
        거주 증명 서류를 통해 받는 분을 인증하는 절차입니다. 해당 절차는 일본 현지 법 규정상 <br className='desktop'/>
        필요한 법적 필수사항에 근거한 것이며, 본인을 인증하는 것 이외에 다른 용도로 활용되지 않습니다.
      </p>
      <p>수취인이 법인 계좌일 때, 증빙서류를 받기 어려우신 경우 일단 신청 후 모인으로 문의주시면 안내해드리겠습니다.</p> 
      <div className='radioBtns'>
        <input type='radio' name='recipientType' id='recipientType1'
          tabIndex={ tabIndex } onChange={ this.onChangePhotoType.bind(this, null) }
          value='1' defaultChecked='true' />
        <label htmlFor='recipientType1'>
          <p>
            <span></span>
            개인 계좌
          </p>
        </label>
        <input type='radio' name='recipientType' id='recipientType2'
          tabIndex={ tabIndex } onChange={ this.onChangePhotoType.bind(this, 6) }
          value='2'/>
        <label htmlFor='recipientType2'>
          <p>
            <span></span>
            법인 계좌
          </p>
        </label>
      </div>
      { this.renderSelecter() }
    </div>
    );
  }
}

RemitFormRecipientsPhoto.propTypes = {
  dispatch: PropTypes.func,
  component: PropTypes.object,
  view: PropTypes.string,
  recipient: PropTypes.object,
  isUpdate: PropTypes.bool,
  focusable: PropTypes.bool,
};

const mapState = state => ({
  component: state.remit.component,
  recipient: state.remit.recipient,
});

export default connect(mapState)(RemitFormRecipientsPhoto);


