import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import MoinDropdown from './MoinDropdown';
import { IMG_URL } from '../../model';

export default class MoinUnitDropdown extends MoinDropdown {
  constructor(props) {
    super(props);

    this.state = {
      open: false,
      selectedIndex: 0,
    };

    if (props.value) {
      this.props.items.forEach((item, index) => {
        if (item.value === props.value) {
          this.state.selectedIndex = index;
        }
      });
    }
  }

  onItemSelect(index) {
    this.setState({
      open: false,
      selectedIndex: index,
    });

    if (this.props.onChange) {
      this.props.onChange(this.props.items[index].value);
    }
  }

  render() {
    const { items } = this.props;
    const className = classnames('dropdown', { open: this.state.open });
    const selectedItem = items[this.state.selectedIndex];

    return(
      <div className={ className }>
        <a className='dropdown-toggle'
           onClick={ this.onOpen.bind(this) }>
          <div className='pull-left moin-unit-label'>
            <div className='moin-body moin-unit-country'>
              { selectedItem.title }
            </div>
            <div id='receive-unit' className='color-skyblue moin-unit-text'>
              { selectedItem.value }
            </div>
          </div>
          <img src={ `${ IMG_URL }/ic-currency.svg` }
               className='moin-unit-toggle pull-right'>
          </img>
          <div className='clearfix'></div>
        </a>
        <ul className='dropdown-menu' role='menu'>
          {items.map((item, index) => (
            <li key={ item.value }>
              <a onClick={ this.onItemSelect.bind(this, index) }>
                { item.title + '(' + item.value + ')' }
              </a>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

MoinUnitDropdown.propTypes = {
  items: PropTypes.array,
  value: PropTypes.any,
};


