import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { connect } from 'react-redux';
import { pushView, updateRemitRecipient } from '../action';
import RemitFormComponent from './RemitFormComponent';
import RemitFormAccount from './RemitFormAccount';
import RemitUpdateAccount from './RemitUpdateAccount';
import RemitFormPhotoUpload from './RemitFormPhotoUpload';
import RemitFormRecipients from './RemitFormRecipients';
import RemitFormRecipientsAdd from './RemitFormRecipientsAdd';
import RemitFormRecipientsPhoto from'./RemitFormRecipientsPhoto';
import { updateRemit } from '../model';

class RemitForm extends Component {
  constructor(props) {
    super(props);
    const isUpdate = this.props.view === 'remit/update';

    this.state = {
      prevIndex: -1,
      sectionIndex: 0,
      isAllValid: false,
      vAccountToken: 0,
      vRecipientToken: 0,
      isUpdate,
    };
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.remit.recipient !== nextProps.remit.recipient) {
      this.setState({ isAllValid: this.checkAllValid(nextProps) });
    }
  }

  onSectionPrev() { this.onSectionChange(this.state.sectionIndex - 1); }
  onSectionNext() {
    let { sectionIndex, isUpdate } = this.state;

    if (!this.validate(isUpdate ? sectionIndex + 1 : sectionIndex)) return;

    this.onSectionChange(sectionIndex + 1);
  }

  onSectionChange(sectionIndex) {
    const { isUpdate } = this.state;
    const newState = {
      prevIndex: this.state.sectionIndex,
      sectionIndex,
    };

    if (this.props.remit.component.unit === 'JPY' && (sectionIndex === 4 || (isUpdate && sectionIndex === 3))) {
      if(!this.validate(3)) newState.sectionIndex = sectionIndex-1;
    }

    if (sectionIndex === 3 || (isUpdate && sectionIndex === 2)) {
      newState.isAllValid = this.checkAllValid();
    }

    this.setState(newState);
  }

  validateAll() {
    const { isUpdate } = this.state;

    for (const sectionIndex of [1,2,3]) {
      if (!this.validate(sectionIndex)) {
        this.onSectionChange(isUpdate ? sectionIndex - 1 : sectionIndex);
        return false;
      }
    }

    return true;
  }

  onRemit() {
    if (!this.validateAll()) return;

    this.props.dispatch(pushView('remit/02'));
  }

  async onRemitUpdate() {
    if (!this.validateAll()) return;

    const profileFields = [
      'firstname', 'lastname', 'phone1', 'phone2', 'country',
      'zip', 'address', 'subaddress', 'account', 'bank',
    ];
    const recipientFields = [
      'id', 'accountname', 'unit', 'phone1', 'phone2',
      'country', 'zip', 'bank', 'branch', 'account', 'address',
      'photoType',' photoFront', 'photoBack',
    ];

    const { remitId, profile, photo, recipient } = this.props.remit;
    const info = {
      id: remitId,
      'update-persontype': photo.persontype,
      'update-idcard': photo.idcardUrl,
      'update-profile': photo.profileUrl,
    };

    profileFields.forEach(field => { info[`update-${ field }`] = profile[field]; });
    recipientFields.forEach(field => { info[`recipient-${ field }`] = recipient[field]; });

    const result = await updateRemit(info);

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      window.alert('수정되었습니다.');

      this.props.dispatch(updateRemitRecipient({}));
      this.props.dispatch(pushView('main'));
    }
  }

  onClose() {
    this.props.dispatch(pushView('main'));
  }

  validateAccount(updateToken = true, props = this.props) {
    const { firstname, lastname, phone2, zip, address, account, bank } = props.remit.profile;

    if (firstname && lastname && phone2 && zip && address && account && bank) {
      return true;
    } else if (!updateToken) {
      return false;
    }

    this.setState({
      vAccountToken: this.state.vAccountToken + 1,
    });

    return false;
  }

  validatePhoto(props = this.props) {
    const { profileUrl, idcardUrl } = props.remit.photo;

    if (profileUrl && idcardUrl) return true;

    window.alert('사진 두 장이 모두 필요합니다.');
    return false;
  }

  validateRecipient(updateToken = true, props = this.props) {
    const { accountname, bank, account, phone2, address, zip } = props.remit.recipient;

    if (accountname && bank && account && phone2 && address && zip) {
      return true;
    } else if (!updateToken) {
      return false;
    }

    this.setState({
      vRecipientToken: this.state.vRecipientToken + 1,
    });

    return false;
  }

  validate(i) {
    switch (i) {
    case 0: return true;
    case 1: return this.validateAccount();
    case 2: return this.validatePhoto();
    case 3: return this.validateRecipient();
    }

    return false;
  }

  checkAllValid(props = this.props) {
    return (
      this.validateAccount(false, props) &&
      this.validatePhoto(props) &&
      this.validateRecipient(false, props)
    );
  }

  renderHeader() {
    return (
      <div className='remit-header'>
        <a onClick={ this.onClose.bind(this) }>
          나가기
        </a>
      </div>
    );
  }

  renderFooter(views) {
    const { isUpdate, isAllValid, sectionIndex } = this.state;
    const submitClassName = classnames('submit', { disabled: !isAllValid });

    return (
      <div className='remit-footer'>
        <div>
          <button type='button'
              className={ classnames('prev', { invisible: sectionIndex === 0 }) }
              onClick={ this.onSectionPrev.bind(this) }>
            이전
          </button>
          <ul>
            { views.map( (obj, index) => (
              <li key={ `remit-bar-${index}` }
                  className={ classnames('remit-bar', {
                    'remit-bar-active': index <= sectionIndex,
                  }) }
                  onClick={ this.onSectionChange.bind(this, index) }>
                <span className='desktop'>{ index + 1 }</span>
              </li>
            ))}
          </ul>
          { 
            views.length === sectionIndex + 1 ? ( isUpdate ?
              <button type='button' className={ submitClassName }
                  onClick={ this.onRemitUpdate.bind(this) }>
                수정
              </button> : 
              <button type='button' className={ submitClassName }
                  onClick={ this.onRemit.bind(this) }>
                <span className='desktop'>송금 </span>신청
              </button> ) :
              <button type='button' className='next'
                  onClick={ this.onSectionNext.bind(this) }>
                다음
              </button>
          }
        </div>
      </div>
    );
  }

  render() {
    const { sectionIndex, prevIndex, vAccountToken, vRecipientToken } = this.state;

    const views = this.state.isUpdate ? (
      this.props.remit.component.unit === 'CNY' ?
    [
      <RemitUpdateAccount
        isUpdate={ true } vToken={ vAccountToken } focusable={ sectionIndex === 0 }>
      </RemitUpdateAccount>,
      <RemitFormPhotoUpload
        isUpdate={ true } focusable={ sectionIndex === 1 }>
      </RemitFormPhotoUpload>,
      <RemitFormRecipientsAdd
        isUpdate={ true } vToken={ vRecipientToken } focusable={ sectionIndex === 2 }>
      </RemitFormRecipientsAdd>,
    ] : [
      <RemitFormAccount
        isUpdate={ true } vToken={ vAccountToken } focusable={ sectionIndex === 0 }>
      </RemitFormAccount>,
      <RemitFormPhotoUpload
        isUpdate={ true } focusable={ sectionIndex === 1 }>
      </RemitFormPhotoUpload>,
      <RemitFormRecipientsAdd
        isUpdate={ true } vToken={ vRecipientToken } focusable={ sectionIndex === 2 }>
      </RemitFormRecipientsAdd>,
      <RemitFormRecipientsPhoto
        isUpdate={ true } focusable={ sectionIndex === 3 }>
      </RemitFormRecipientsPhoto>,
    ]) : (
      this.props.remit.component.unit === 'CNY' ?
    [
      <RemitFormComponent focusable={ sectionIndex === 0 }></RemitFormComponent>,
      <RemitFormAccount
        isUpdate={ false } vToken={ vAccountToken } focusable={ sectionIndex === 1 }>
      </RemitFormAccount>,
      <RemitFormPhotoUpload
        isUpdate={ false } focusable={ sectionIndex === 2 }>
      </RemitFormPhotoUpload>,
      <RemitFormRecipients
        isUpdate={ false } vToken={ vRecipientToken } focusable={ sectionIndex === 3 }>
      </RemitFormRecipients>,
    ] : [
      <RemitFormComponent focusable={ sectionIndex === 0 }></RemitFormComponent>,
      <RemitFormAccount
        isUpdate={ false } vToken={ vAccountToken } focusable={ sectionIndex === 1 }>
      </RemitFormAccount>,
      <RemitFormPhotoUpload
        isUpdate={ false } focusable={ sectionIndex === 2 }>
      </RemitFormPhotoUpload>,
      <RemitFormRecipients
        isUpdate={ false } vToken={ vRecipientToken } focusable={ sectionIndex === 3 }>
      </RemitFormRecipients>,
      <RemitFormRecipientsPhoto
        isUpdate={ false } focusable={ sectionIndex === 4 }>
      </RemitFormRecipientsPhoto>,
    ]);

    return (
      <div className='themoin-remit-form'>
        { this.renderHeader() }
        <div className='section'>
          {views.map((view, i) => {
            const className = classnames({
              'hidden-left': i < sectionIndex,
              'hidden-right': i > sectionIndex,
              animate: i === sectionIndex || i === prevIndex,
            });

            return (
              <div key={ `view-${i}` } className={ className }>
                { view }
              </div>
            );
          })}
        </div>
        { this.renderFooter(views) }
      </div>
    )
  }
}

RemitForm.propTypes = {
  dispatch: PropTypes.func,
  view: PropTypes.string,
  remit: PropTypes.object,
  component: PropTypes.object,
};

RemitForm.mapState = state => ({
  view: state.view,
  remit: state.remit,
  recipient: state.remit.recipient,
});

RemitForm = connect(RemitForm.mapState)(RemitForm);
export default RemitForm;


