import React, { Component } from 'react';
import PropTypes from 'prop-types';
import update from 'react-addons-update';
import { connect } from 'react-redux';
import { getEmptyString } from '../util';
import { pushView } from '../action';
import {
  fetchRecipients,
  addRecipient,
  updateRecipient,
} from '../model';
import MoinDropdown from './components/MoinDropdown';
import MoinTextInput from './components/MoinTextInput';
import RecipientsBankPopup from './RecipientsBankPopup';

export const DEFAULT_RECIPIENT_JPY = {
  unit: 'JPY',
  accountname: '',
  bank: '',
  branch: '',
  country: '일본 (Japan)',
  account: '',
  phone1: '+81 (일본)',
  phone2: '',
  address: '',
  zip: '',
  id: null,
  user_id: null,
};

export const DEFAULT_RECIPIENT_CNY = {
  unit: 'CNY',
  accountname: '',
  bank: '',
  branch: '',
  country: '중국 (China)',
  account: '',
  phone1: '+86 (중국)',
  phone2: '',
  address: '',
  zip: '',
  id: null,
  user_id: null,
};

const PLACEHOLDER_JPY = {
  accountname: 'ホンギルトン',
  zip: '01234',
  address: '東京都 渋谷区 西原 (Nishihara Shibuya-ku Tokyo)',
};

const PLACEHOLDER_CNY = {
  accountname: '은행계좌에 등록된 예금주 이름을 정확하게 입력해주세요.',
  zip: '434000',
  address: '湖北省荆州市红苑大酒店 李有财',
};

export class RecipientsUpdate extends Component {
  constructor(props) {
    super(props);

    const { recipients, recipientId } = props;
    const isUpdate = props.view === 'recipients/update';
    const recipient = isUpdate ?
      Object.assign({}, recipients.filter(_recipient => _recipient.id === recipientId)[0]) :
      DEFAULT_RECIPIENT_JPY;
    const unit = isUpdate ? recipient.unit : 'JPY';
    const KEYS = unit === 'JPY' ?
      Object.keys(DEFAULT_RECIPIENT_JPY) :
      Object.keys(DEFAULT_RECIPIENT_CNY);

    const validityMap = KEYS.reduce((map, key) => {
      map[key] = true;
      return map;
    }, {});

    this.state = {
      vToken: 0,
      vBankToken: 0,
      isUpdate,
      recipient,
      unit,
      validityMap,
    };
  }

  onSubmit(e) {
    e.preventDefault();

    this.setState({
      vToken: this.state.vToken + 1,
    }, this.submit.bind(this));
  }

  async submit() {
    const { recipient, validityMap, isUpdate } = this.state;
    const { accountname, unit, phone1, phone2, country, bank,
      zip, address, branch, account, id, user_id } = recipient;

    const isValid = Object.keys(validityMap).every(key => validityMap[key]);

    if (!isValid) {
      window.alert('입력 내용을 다시 한 번 확인해주세요.');
      return;
    }

    const info = {
      accountname, unit, phone1, phone2, country, bank,
      zip, address, branch, account, id, user_id,
    };

    const result = isUpdate ?
      await updateRecipient(info) :
      await addRecipient(info);

    if (result.ret === 'failure') {
      window.alert(result.msg);
      return;
    }

    const message = isUpdate ? '수정되었습니다.' : '추가되었습니다.';
    window.alert(message);

    const { data: { recipients } } = await fetchRecipients();

    this.props.dispatch(pushView('recipients', { recipients }));
  }

  onUnitChange(unit) {
    const [phone1, country] = unit === 'JPY' ?
      ['+81 (일본)', '일본 (Japan)'] :
      ['+86 (중국)', '중국 (China)'];

    const state = update(this.state, {
      recipient: {
        phone1: { $set: phone1 },
        country: { $set: country },
        unit: { $set: unit },
        bank: { $set: '' },
      },
      unit: { $set: unit },
    });

    this.setState(state);
  }

  onTextChange(name, e) {
    const { value, valid } = e;
    const newState = {
      validityMap: { [name]: { $set: valid }},
    };

    if (value !== null) newState.recipient = { [name]: { $set: value }};

    this.setState(update(this.state, newState));
  }

  onBankValue(bank) {
    const state = update(this.state, {
      vBankToken: { $set: this.state.vBankToken + 1 },
      recipient: { bank: { $set: bank }},
    });

    this.setState(state);
  }

  renderFormSubContentsJPY() {
    const tabIndex = this.props.focusable ? 0 : -1;
    const { recipient, vToken, vBankToken } = this.state;

    return (
    <div>
    <div className='form-row col-2'>
      <MoinTextInput
        label='은행'
        placeholder='유초 은행 (Japan Post Bank)'
        vToken={ vToken + vBankToken }
        value={ recipient.bank }
        postValidators={[ MoinTextInput.EmptyValidator ]}
        tabIndex={ tabIndex }
        onChange={ this.onTextChange.bind(this, 'bank') }>
      </MoinTextInput>
      <div>
        <button className='btn-searchbank' type='button' data-toggle='modal'
            tabIndex={ tabIndex }
            data-target='#bank-popup'>
          일본 은행 목록
        </button>
      </div>
    </div>
    <div className='form-row col-2'>
      <MoinTextInput
        label='지점'
        placeholder={ '本山支店 (05248, Shibuya)' }
        vToken={ vToken }
        value={ recipient.branch }
        postValidators={[ MoinTextInput.EmptyValidator ]}
        tabIndex={ tabIndex }
        onChange={ this.onTextChange.bind(this, 'branch') }>
      </MoinTextInput>
      <MoinTextInput
        label='계좌 번호'
        placeholder='1234567'
        isNumeric={ true }
        vToken={ vToken }
        value={ recipient.account }
        onChange={ this.onTextChange.bind(this, 'account') }
        tabIndex={ tabIndex }
        postValidators={[ MoinTextInput.EmptyValidator ]}>
      </MoinTextInput>
    </div>
    </div>
    );
  }

  renderFormSubContentsCNY() {
    const tabIndex = this.props.focusable ? 0 : -1;
    const { recipient, vToken } = this.state;

    return (
    <div>
    <div className='form-row col-2'>
      <MoinTextInput
        label='은행'
        placeholder='중국공상은행(ICBC)'
        vToken={ vToken }
        value={ recipient.bank }
        postValidators={[ MoinTextInput.EmptyValidator ]}
        tabIndex={ tabIndex }
        onChange={ this.onTextChange.bind(this, 'bank') }>
      </MoinTextInput>
      <div>
        <button className='btn-searchbank' type='button' data-toggle='modal'
            tabIndex={ tabIndex }
            data-target='#bank-popup'>
          중국 은행 목록
        </button>
      </div>
    </div>
    <div className='form-row'>
      <MoinTextInput
        label='계좌 번호'
        placeholder='1234567'
        isNumeric={ true }
        vToken={ vToken }
        postValidators={[ MoinTextInput.EmptyValidator ]}
        value={ recipient.account }
        tabIndex={ tabIndex }
        onChange={ this.onTextChange.bind(this, 'account') }>
      </MoinTextInput>
    </div>
    </div>
    );
  }

  renderFormContents() {
    const tabIndex = this.props.focusable ? 0 : -1;
    const { recipient, vToken, unit } = this.state;
    const [pc, tip, validator] = unit === 'JPY' ?
      [PLACEHOLDER_JPY, '가타카나로 예금주명을 정확하게 입력해주세요.', MoinTextInput.JpnValidator] :
      [PLACEHOLDER_CNY, '중국어 혹은 한자로 되어있는 예금주명을 정확하게 입력해주세요.', MoinTextInput.ChnEngValidator];

    return (
    <div className='form-contents'>
      <div className='form-row'>
        <MoinTextInput
          label='예금주 이름 (Account Name)'
          placeholder={ pc.accountname }
          tip= { tip }
          vToken={ vToken }
          value={ recipient.accountname }
          postValidators={[ validator ]}
          tabIndex={ tabIndex }
          onChange={ this.onTextChange.bind(this, 'accountname') }>
        </MoinTextInput>
      </div>
      <div className='form-row col-2'>
        <div className='phone1'>
          <MoinDropdown
            label='휴대전화 번호'
            items={ [{ value: recipient.phone1 }] }>
          </MoinDropdown>
        </div>
        <MoinTextInput className='phone2'
          label={ getEmptyString() }
          placeholder='00012345678'
          isNumeric={ true }
          maxLength='11'
          vToken={ vToken }
          value={ recipient.phone2 }
          postValidators={[ MoinTextInput.EmptyValidator ]}
          tabIndex={ tabIndex }
          onChange={ this.onTextChange.bind(this, 'phone2') }>
        </MoinTextInput>
      </div>
      <div className='form-row col-2'>
        <div>
          <MoinDropdown label='국가'
            items={ [{ value: recipient.country }] }>
          </MoinDropdown>
        </div>
        <MoinTextInput
          label='우편번호'
          placeholder={ pc.zip }
          isNumeric={ true }
          maxLength='7'
          vToken={ vToken }
          value={ recipient.zip }
          onChange={ this.onTextChange.bind(this, 'zip') }
          postValidators={[ MoinTextInput.EmptyValidator ]}
          tabIndex={ tabIndex }>
        </MoinTextInput>
      </div>
      <div className='form-row'>
        <MoinTextInput
          label='집 주소'
          placeholder={ pc.address }
          vToken={ vToken }
          value={ recipient.address }
          postValidators={[ MoinTextInput.EmptyValidator ]}
          tabIndex={ tabIndex }
          onChange={ this.onTextChange.bind(this, 'address') }>
        </MoinTextInput>
      </div>
      { this.state.unit === 'JPY' ?
          this.renderFormSubContentsJPY() :
          this.renderFormSubContentsCNY()
      }
      <RecipientsBankPopup onSubmit={ this.onBankValue.bind(this) } unit={ this.state.unit }></RecipientsBankPopup>
    </div>
    );
  }

  render() {
    const { focusable, profile } = this.props;
    const { recipient, isUpdate } = this.state;
    const tabIndex = focusable ? 0 : -1;
    const isAgency = isUpdate && profile.id !== recipient.user_id;

    return (
    <div className='themoin-recipients-update'>
    <div>
      <form action='/a/v1/member/recipients/update' method='post'
          onSubmit={ this.onSubmit.bind(this) }>
        <h1>수령인 { isUpdate ? (isAgency ? '조회' : '수정') : '추가' }</h1>
        <div className='form-row top-spacer'>
          {this.state.isUpdate ?
            null :
            <MoinDropdown label='통화'
              onChange={ this.onUnitChange.bind(this) }
              items={[
                { title: 'JPY', value: 'JPY' },
                { title: 'CNY', value: 'CNY' },
              ]}>
            </MoinDropdown>
          }
        </div>
        { this.renderFormContents() }
        { isAgency ?
          null :
          <div className='form-row top-spacer'>
            <button className='account-send-btn' type='submit' tabIndex={ tabIndex }>
              저장
            </button>
          </div>
        }
      </form>
    </div>
    </div>
    );
  }
}

RecipientsUpdate.defaultProps = {
  focusable: true,
};

RecipientsUpdate.propTypes = {
  dispatch: PropTypes.func,
  view: PropTypes.string,
  recipients: PropTypes.array,
  recipientId: PropTypes.number,
  focusable: PropTypes.bool,
  profile: PropTypes.object,
};

RecipientsUpdate.mapState = state => ({
  view: state.view,
  recipients: state.recipients,
  recipientId: state.recipientId,
  profile: state.profile,
});

export default connect(RecipientsUpdate.mapState)(RecipientsUpdate);



