import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames';
import { pushView } from '../../action';
import {
  IMG_URL,
  fetchRemitDetail,
  deleteRemit,
  cancelRemit,
} from '../../model';

const REMIT_STATUS = {
  0: {
    text: '입금 확인 중',
    cancel: true,
    update: true,
    delete: false,
  },
  1: {
  },
  2: {
    text: '수정 요청',
    cancel: true,
    update: true,
    delete: false,
  },
  3: {
    text: '수취은행 처리중',
    cancel: false,
    update: false,
    delete: false,
  },
  4: {
    text: '송금 중',
    cancel: false,
    update: false,
    delete: false,
  },
  5: {
    text: '송금 완료',
    cancel: false,
    update: false,
    delete: true,
  },
  6: {
    text: '취소 됨',
    cancel: false,
    update: false,
    delete: true,
  },
  7: {
    text: '서류 검토 중',
    cancel: true,
    update: true,
    delete: false,
  },
};

export class MainRemitItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isSimple: true,
      isPopupDetail: false,
    };
  }

  onToggle() {
    if (window.innerWidth <= 700 || this.state.isPopupDetail ) {
      this.setState({ isPopupDetail: !this.state.isPopupDetail });
      return;
    }

    this.setState({ isSimple: !this.state.isSimple });
  }

  onCall(e) {
    e.stopPropagation();
  }

  async onRemitUpdate(remit, e) {
    const { result, data } = await fetchRemitDetail(remit.id);

    if (result.ret === 'failure') {
      this.setState({ errorMessage: result.msg });
    } else {
      const remitData = {
        profile : data.profile,
        photo: {
          persontype: data.profile.persontype,
          profileUrl: data.profile.profile,
          idcardUrl: data.profile.idcard,
        },
        recipient: data.recipient,
        component: {
          unit: data.remit['receive-unit'],
          sendAmount: '1,000,000',
          receiveAmount: '0',
          couponCode: '',
          couponAmount: 0,
        },
        remitId: remit.id,
      };

      this.props.dispatch(pushView('remit/update', { remit: remitData }));
    }
  }
  async onRemitDelete(remit) {
    const result = await deleteRemit(remit.id);

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      window.alert('삭제되었습니다.');
      this.props.onReloadItems();
      this.setState({
        isSimple: true,
        isPopupDetail: false,
      });
    }
  }

  async onRemitCancel(remit) {
    const result = await cancelRemit(remit.id);

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      window.alert('취소되었습니다.');
      this.props.onReloadItems();
      this.setState({
        isSimple: true,
        isPopupDetail: false,
      });
    }
  }

  onRemitEvent(type, remit, e) {
    e.stopPropagation();

    switch (type) {
      case 'delete':
        this.onRemitDelete(remit);
        break;
      case 'cancel':
        this.onRemitCancel(remit);
        break;
      case 'update':
        this.onRemitUpdate(remit);
        break;
    }
  }

  renderSimple() {
    const { remit, index } = this.props;
    const onUpdate = this.onRemitEvent.bind(this, 'update', remit);
    const onDelete = this.onRemitEvent.bind(this, 'delete', remit);
    const onCancel = this.onRemitEvent.bind(this, 'cancel', remit);

    return (
    <div className={ classnames('simple', { 'simple-hidden': !this.state.isSimple }) }>
      <div className='title-index'>{ index }</div>
      <div className='simple-nametime'>
        <h3 className='username'>{ remit.receiver.accountname }</h3>
        <p className='create-time'>{ remit.create_time }</p>
      </div>
      <div className='simple-spacer'></div>
      <div className='simple-amount'>
        <h3>
          <span>{ remit.send_amount }</span>
          <span className='unit'>{ ` ${remit.send_unit}` }</span>
        </h3>
        <div className='arrow'></div>
        <h3>
          <span>{ remit.receive_amount }</span>
          <span className='unit'>{ ` ${remit.receive_unit}` }</span>
        </h3>
      </div>
      <div className='simple-spacer'></div>
        {
          remit.status === 0 ?
            <div className='user-sendlist-status'>
              <div className='user-sendlist-state'>
                <div className='user-sendlist-state-text moin-body'>입금 확인 중</div>
                <a className='user-sendlist-state-cancel moin-body desktop'
                   onClick={ onCancel } >송금 취소</a>
              </div>
              <button type='button desktop' className='user-sendlist-btn back-deepgrey'
                  onClick={ onUpdate } >수정</button>
            </div> : null
        }
      {
        remit.status === 7 ?
          <div className='user-sendlist-status'>
            <div className='user-sendlist-state'>
              <div className='user-sendlist-state-text moin-body'>서류 검토 중</div>
              <a className='user-sendlist-state-cancel moin-body desktop'
                 onClick={ onCancel } >송금 취소</a>
            </div>
            <button type='button desktop' className='user-sendlist-btn back-deepgrey'
                    onClick={ onUpdate } >수정</button>
          </div> : null
      }
        {
          remit.status === 2 ?
            <div className='user-sendlist-status'>
              <div className='user-sendlist-state'>
                <div className='user-sendlist-state-text moin-body'>수정 요청</div>
                <a className='user-sendlist-state-cancel moin-body'
                   onClick={ onCancel } >송금 취소</a>
              </div>
              <button type='button' className='user-sendlist-btn back-deepgrey'
                  onClick={ onUpdate } >수정</button>
            </div> : null
        }
        {
          remit.status === 3 ?
            <div className='user-sendlist-status'>
              <div className='user-sendlist-state-text moin-body'>수취은행 처리중</div>
              <div className='more'>
                <img style={{marginBottom: 2}} src='/img/btn-more.png'/>
              </div>
            </div> : null
        }
        {
          remit.status === 4 ?
            <div className='user-sendlist-status'>
              <div className='user-sendlist-state'>
                <div className='user-sendlist-state-text moin-body'>송금 중</div>
              </div>
              <div className='more'>
                <img style={{marginBottom: 2}} src='/img/btn-more.png'/>
              </div>
            </div> : null
        }
        {
          remit.status === 5 ?
            <div className='user-sendlist-status'>
              <div className='user-sendlist-state'>
                <div className='user-sendlist-state-text moin-body'>송금 완료</div>
              </div>
              <a className='user-sendlist-state-delete moin-body desktop'
                 onClick={ onDelete } >내역 삭제</a>
            </div> : null
        }
        {
          remit.status === 6 ?
            <div className='user-sendlist-status'>
              <div className='user-sendlist-state'>
                <div className='user-sendlist-state-text moin-body'>취소 됨</div>
              </div>
              <a className='user-sendlist-state-delete moin-body'
                 onClick={ onDelete } >내역 삭제</a>
            </div> : null
        }
    </div>
    );
  }

  renderDetail() {
    const { remit, index } = this.props;
    const onUpdate = this.onRemitEvent.bind(this, 'update', remit);
    const onDelete = this.onRemitEvent.bind(this, 'delete', remit);
    const onCancel = this.onRemitEvent.bind(this, 'cancel', remit);
    const renderButtons = () => {
      switch (remit.status) {
        case 0:
        case 7:
          return (
            <div className='user-sendlistdetail-link'>
              <button type='button' onClick = { onUpdate } className='user-sendlistdetail-btn back-deepgrey'>수정</button>
              <a onClick = { onCancel } className='user-sendlist-state-cancel moin-body'>송금 취소</a>
            </div>
          );
        case 5:
        case 6:
          return (
            <div className='user-sendlistdetail-link'>
              <a onClick = { onDelete } className='user-sendlist-state-cancel moin-body'>내역 삭제</a>
            </div>
          );
        default:
          return (
            <div className='user-sendlistdetail-link'>
              <div className='user-sendlist-roundbox'>문의전화</div>
              <a className='user-sendlist-call'
                 href='tel:07050892017'
                 onClick={ this.onCall.bind(this) }>070-5089-2017</a>
            </div>
          );
      }
    };

    return (
    <div className={ classnames('detail', { 'detail-hidden': this.state.isSimple }) }
        style={{ paddingLeft: '10px' }}>
      <div className='user-sendlistdetail' onClick={ this.onToggle.bind(this) }>
        <div className='user-sendlist-num'>{ index }</div>
        <div className='user-sendlistdetail-info'>
          <h3 className='color-deepblue user-sendlistdetail-name'>{remit.receiver.accountname}</h3>
          <div className='user-sendlistdetail-account'>
            <div className='moin-body'>
              <span className='pull-left'>전화번호 : {remit.receiver.phone1} {remit.receiver.phone2}</span>
              <span className='pull-left body-bar' />
              <span className='pull-left'>국가 : {remit.receiver.country}</span>
              <div className='clearfix' />
            </div>
            <div className='moin-body'>
              <span className='pull-left'>우편번호 : {remit.receiver.zip}</span>
              <span className='pull-left body-bar' />
              <span className='pull-left'>주소 : {remit.receiver.subaddress} {remit.receiver.address}</span>
              <div className='clearfix' />
            </div>
            <div className='moin-body'>
              <span className='pull-left'>은행 : {remit.receiver.bank}</span>
              <span className='pull-left body-bar' />
              <span className='pull-left'>계좌 : {remit.receiver.account}</span>
              <div className='clearfix' />
            </div>
          </div>
          <div className='user-sendlistdetail-amount'>
            <h3 className='color-deepblue user-sendlist-send pull-left'>
              <span className='user-sendlist-send color-deepblue'>{remit.send_amount}</span>
              <span className='user-sendlist-sendunit'>{remit.send_unit}</span>
            </h3>
            <img src={ `${ IMG_URL }/ic-next-p.png` } className='user-sendlist-ic pull-left' />
            <h3 className='color-deepblue user-sendlist-recieve pull-left'>
              <span className='user-sendlist-recieve color-skyblue'>{remit.receive_amount}</span>
              <span className='user-sendlist-recieveunit'>{remit.receive_unit}</span>
            </h3>
            <div className='clearfix' />
          </div>
          <div className='user-sendlistdetail-state'>
            <div className='user-sendlistdetail-state-text pull-left moin-body'>{REMIT_STATUS[remit.status].text}</div>
            <span className='pull-left body-bar' />
            <div className='user-sendlistdetail-date pull-left moin-body'>{remit.create_time}에 신청함</div>
            <div className='clearfix' />
          </div>
        </div>
        { renderButtons() }
      </div>
    </div>
    );
  }

  renderAmount() {
    const { remit } = this.props;

    return (
    <div className='user-sendlistdetail-amount'>
      <h3 className='color-deepblue user-sendlist-send pull-left'>
        <span className='user-sendlist-send color-deepblue'>{remit.send_amount}</span>
        <span className='user-sendlist-sendunit'>{remit.send_unit}</span>
      </h3>
      <img src={ `${ IMG_URL }/ic-next-p.png` } className='user-sendlist-ic pull-left' />
      <h3 className='color-deepblue user-sendlist-recieve pull-left'>
        <span className='user-sendlist-recieve color-skyblue'>{remit.receive_amount}</span>
        <span className='user-sendlist-recieveunit'>{remit.receive_unit}</span>
      </h3>
      <div className='clearfix' />
    </div>
    );
  }

  renderDetailPopup() {
    const { remit } = this.props;
    const onUpdate = this.onRemitEvent.bind(this, 'update', remit);
    const onDelete = this.onRemitEvent.bind(this, 'delete', remit);
    const onCancel = this.onRemitEvent.bind(this, 'cancel', remit);
    const modifyTime = remit.modify_time ? remit.modify_time : remit.create_time;
    const renderButtons = () => {
      switch (remit.status) {
      case 0:
      case 7:
        return (
          <div className='buttons'>
            <a className='cancel' onClick={ onCancel }>송금 취소</a>
            <a className='modify' onClick={ onUpdate }>수정</a>
          </div> 
        );
      case 5:
      case 6:
        return (
          <div className='buttons'>
            <a className='delete' onClick={ onDelete }>내역 삭제</a>
          </div>
        );
      default:
        return (
          <div className='buttons'>
            <a className='call'
                href='tel:07050892017'
                onClick={ this.onCall.bind(this) }>
              전화 문의하기
            </a>
          </div>
        );
      }
    };

    const { phone1, phone2, country, zip, subaddress, address, bank, account } = remit.receiver;
    const captions = [
      { label: '전화번호', value: `${phone1} ${phone2}` },
      { label: '국가', value: country },
      { label: '우편번호', value: zip },
      { label: '주소', value: `${subaddress ? subaddress : ''} ${address}` },
      { label: '은행', value: bank },
      { label: '계좌', value: account },
    ];

    return (
    <div className='detail-popup'>
      <div className='popup-content'>
        <h1 className='accountname'>{ remit.receiver.accountname }</h1>
        {captions.map((caption, index) => (
          <div className='caption' key={ `caption-${index}` }>
            { `${caption.label} ${caption.value}` }
          </div>
        ))}
        <div className='divider'></div>
        { this.renderAmount() }
        <div className='divider'></div>
        <h2 className='status'>{ REMIT_STATUS[remit.status].text }</h2>
        <div className='modify-time caption'>{ `${ modifyTime }에 업데이트 됨` }</div>
        { renderButtons() }
      </div>
      <a className='popup-close'> {/* close reserved by bootstrap... */}
        닫기
      </a>
    </div>
    );
  }

  render() {
    return (
    <div className='themoin-main-remititem' onClick={ this.onToggle.bind(this) }>
      { this.renderSimple() }
      { this.renderDetail() }
      { this.state.isPopupDetail ? this.renderDetailPopup() : null }
    </div>
    );
  }
}

MainRemitItem.propTypes = {
  dispatch: PropTypes.func,
  index: PropTypes.number,
  remit: PropTypes.object,
  onReloadItems: PropTypes.func,
};

MainRemitItem.mapState = state => ({
});

MainRemitItem = connect(MainRemitItem.mapState)(MainRemitItem);

export default MainRemitItem;


