import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
//import { update } from 'react-addons-update'; TODO!
import { connect } from 'react-redux';
import { pushView } from '../action';
import {
  setFeeInfo,
  insertComma,
  changeSendAmount,
  changeReceiveAmount,
} from '../util';
import { IMG_URL, fetchRemitFeeInfo } from '../model';
import { updateRemitComponent } from '../action';
import MoinUnitDropdown from './components/MoinUnitDropdown';

export class RemitComponent extends Component {
  static getExchangeRateUrl() {
    const access_key = '70f3655421d3b5bdf773aeb0d34dc2a1';
    const currencies = 'KRW,JPY,CNY';
    const source = 'USD';
    const format = '1';

    return `https://apilayer.net/api/live?access_key=${access_key}&currencies=${currencies}&source=${source}&format=${format}`;
  }

  constructor(props) {
    super(props);

    const indexMoneyRanks = [
      {
        id: 'rank-moin', className: 'index-money-moin',
        title: '모        인'.replace(/ /g, '\u00a0'),
      },
      { id: 'rank-kb', title: '국민은행' },
      { id: 'rank-hana', title: '하나은행' },
      { id: 'rank-shinhan', title: '신한은행' },
      { id: 'rank-woori', title: '우리은행' },
      {
        id: 'rank-moin-verify', className: 'index-money-moin-event',
        title: '학생인증',
      },
    ].map(item => {
      item.amount = '0';
      return item;
    });

    this._newState = null;

    this.state = {
      exchangeRates: {},
      feedbackText: null,
      feedbackHidden: true,
      feedbackEventAmount: 0,
      indexMoneyRanks,
    };
  }

  componentWillReceiveProps(nextProps) {
    if (this._newState) {
      this.setState(this._newState);
      this._newState = null;
    }
  }

  componentDidMount() {
    this.loadExchangeRates();
  }

  async loadfeeInfo() {
    const { data } = await fetchRemitFeeInfo();

    setFeeInfo(data);
  }

  async loadExchangeRates() {
    const url = RemitComponent.getExchangeRateUrl();
    const response = await fetch(url);
    const { quotes } = await response.json();
    await this.loadfeeInfo();

    exchangerates['USDKRW'] = quotes.USDKRW;
    exchangerates['JPYKRW'] = quotes.USDKRW / quotes.USDJPY;
    exchangerates['CNYKRW'] = quotes.USDKRW / quotes.USDCNY;
    console.log(JSON.stringify(exchangerates));

    const { unit } = this.props.component;
    this.onAmountChange({ isSend: true, comma: true, unit });
  }

  onUnitChange(unit) {
    this.onAmountChange({ isSend: true, comma: true, unit });
  }

  onSend(e) {
    const { unit } = this.props.component;
    this.onAmountChange({ isSend: true, comma: true, unit });

    this.props.dispatch(pushView('remit/01'));

    // alert("현재 신규 업종 등록 과정 진행 중이어서 송금 신청이 불가능합니다. 송금은 7월 18일 부터 재개될 예정입니다. 이용에 불편드려 죄송합니다.");

    e.preventDefault();
    return false;
  }

  onAmountChange({ unit, isSend, couponAmount, couponCode, comma }, e) {
    const newState = {};
    const { sendAmount } = this.props.component;

    if (!couponCode) {
      couponCode = this.props.component.couponCode;
      couponAmount = this.props.component.couponAmount;
    } else if (couponCode === 'cancel') {
      couponCode = '';
      couponAmount = 0;
    }

    const MAX_AMOUNT = 2000000;
    const EVENT_AMOUNT = 0;

    const amount = e ? e.target.value : sendAmount.toString();
    const intAmount = amount.replace(/[^0-9]/g, "") * 1;

    // TODO: moin.js
    let intSend;
    let intReceive;
    let receiveAmounts;
    let preview = {};

    this.state.indexMoneyRanks.map(item => {
      item.intamount = 0;
      return item;
    });

    const updateSendAmount = (updateOnMax = false) => {
      //가격한도300만원
      if (intSend >= MAX_AMOUNT) {
        intSend = MAX_AMOUNT;

        newState.feedbackText = '1회 송금 한도는 200만원까지 입니다.';
        newState.feedbackHidden = false;

        if (updateOnMax) {
          const result = changeSendAmount('KRW', unit, intSend, couponAmount);
          receiveAmounts = result.receiveAmounts;
          preview = result.preview;
          intReceive = 0;
        }
      } else {
        newState.feedbackText = null;
        newState.feedbackHidden = intSend > EVENT_AMOUNT;
      }
      //일본이면 경고
      newState.feedbackHidden = unit === 'JPY' ? false : newState.feedbackHidden;
    };

    if (isSend) {
      intSend = intAmount;
      updateSendAmount();
      const result = changeSendAmount('KRW', unit, intSend, couponAmount);
      receiveAmounts = result.receiveAmounts;
      preview = result.preview;
      intReceive = 0;
    } else {
      intReceive = intAmount;
      const result = changeReceiveAmount('KRW', unit, intReceive, couponAmount);
      receiveAmounts = result.resultAmounts['receiveAmounts'];
      preview = result.preview;
      intSend = result.resultAmounts['moinSendamount'];
      updateSendAmount(true);
    }

    newState.preview = preview;
    newState.feedbackEventAmount = receiveAmounts[0].receive_amount
      - receiveAmounts[1].receive_amount;

    //index페이지 은행별 가격비교 에니메이션
    for (var index in receiveAmounts) {
      var width = 100;
      width = (receiveAmounts[index]['receive_amount'] - receiveAmounts[receiveAmounts.length - 2]['receive_amount'])
        / (receiveAmounts[0]['receive_amount'] - receiveAmounts[receiveAmounts.length - 2]['receive_amount']) * width / 2 + width / 2;

      const rank = this.state.indexMoneyRanks.filter(a => a.id === `rank-${receiveAmounts[index]['id']}`)[0];
      rank.width = `${width}%`;
      rank.amount = insertComma(receiveAmounts[index]['receive_amount']);
      rank.intamount = receiveAmounts[index]['receive_amount'];
    }

    newState.indexMoneyRanks = this.state.indexMoneyRanks.sort((a, b) => b.intamount - a.intamount);
    intReceive = newState.indexMoneyRanks[0].amount;
    //보내는금액 받는금액 입력

    this._newState = newState;

    const newComponent = {
      sendAmount: comma || !isSend ? insertComma(intSend) : intSend,
      receiveAmount: comma || isSend ? insertComma(intReceive) : intReceive,
      unit,
      couponCode,
      couponAmount,
      fee: preview.moin.fee,
      rate: preview.moin.rate,
    };

    this.props.dispatch(updateRemitComponent(newComponent));
  }

  onAmountBlur(isSend, e) {
    const amount = insertComma(e.target.value);
    const newComponent = Object.assign({}, this.props.component);

    if (isSend) {
      newComponent.sendAmount = amount;
    } else {
      newComponent.receiveAmount = amount;
    }

    this.props.dispatch(updateRemitComponent(newComponent));
  }

  render() {
    const { unit, sendAmount, receiveAmount } = this.props.component;
    const { feedbackHidden, feedbackText } = this.state;
    const moinEventAmountClassName = classnames(
      'moin-event-amount', {
        hidden: feedbackHidden,
      }
    );

    return (
      //TODO : Event msg refactoring, add student verifing msg
      <div className='themoin-remit-component back-grey'>
        <div id='moin-event-amount' className={ moinEventAmountClassName }>
          <p className='color-deepgrey'>
            { feedbackText !== null ? feedbackText : '' }
          </p>
          {/*
            수수료 0% 이벤트가 적용되어 <br className='mobile'></br>실제로 받는 금액이&nbsp;
            <span className='color-deepblue'>{ this.state.feedbackEventAmount }엔</span>
            더 늘어납니다.
          */}
        </div>
        <div className='moin-amount'>
          <div className='amount'>
            <form id='moin-remit' onSubmit={this.onSend.bind(this)}>
              <div className='index-send'>
                <div className='index-amount'>
                  <div className='index-amount-label moin-body'>
                    내가 보내는 금액
                </div>
                  <input type='text'
                    className='index-amount-input'
                    onChange={this.onAmountChange.bind(this, { isSend: true, unit })}
                    onBlur={this.onAmountBlur.bind(this, true)}
                    value={sendAmount} />
                </div>
                <div className='dropdown index-unit'>
                  <a className='dropdown-toggle' data-toggle='dropdown'
                    role='button' aria-expanded='false'>
                    <div className='index-unit-label pull-left'>
                      <div className='index-unit-country moin-body'>대한민국</div>
                      <div id='send-unit' className='index-unit-text color-deepblue'>KRW</div>
                    </div>
                    <img src={ `${ IMG_URL }/ic-currency-darkblue.svg` }
                      className='index-unit-toggle pull-right'>
                    </img>
                  </a>
                  <ul className='dropdown-menu' role='menu'>
                    <li><a>대한민국(KRW)</a></li>
                  </ul>
                </div>
              </div>
              <div className='index-receive'>
                <div className='index-amount'>
                  <div className='index-amount-label moin-body'>
                    실제로 받는 금액
                </div>
                  <input type='text'
                    className='index-amount-input moin-title color-skyblue'
                    onChange={this.onAmountChange.bind(this, { isSend: false, unit })}
                    onBlur={this.onAmountBlur.bind(this, false)}
                    value={receiveAmount} />
                </div>
                <div className='index-unit back-skyblue'>
                  <MoinUnitDropdown label='통화'
                    onChange={this.onUnitChange.bind(this)}
                    value={unit}
                    items={[
                      { title: '일본', value: 'JPY' },
                      //{ title: '중국', value: 'CNY' },
                    ]}>
                  </MoinUnitDropdown>
                </div>
              </div>
              <button type='submit'
                className='index-send-btn moin-body'>
                송금하기
            </button>
            </form>
          </div>
          <div className='money-graph'>
            <h3 className='index-money-title'>실제로 받는 금액</h3>
            <div className='index-money-rank'>
              {this.state.indexMoneyRanks.map(item => (
                <div key={item.id} id={item.id}
                  className={item.className ? item.className : 'index-money-bank'}>
                  <div className='index-money-name'>{item.title}</div>
                  <div className='index-money-bar'>
                    <div style={{ width: item.width, height: '100%' }}></div>
                  </div>
                  <div className='index-money-amount'>{item.amount}</div>
                  <div className='index-money-unit'>{unit}</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

RemitComponent.propTypes = {
  dispatch: PropTypes.func,
  component: PropTypes.object,
};

RemitComponent.mapState = state => ({
  component: state.remit.component,
});

export default connect(RemitComponent.mapState)(RemitComponent);



