import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  pushView,
  deleteRecipient as aDeleteRecipient,
} from '../action';
import { IMG_URL, deleteRecipient } from '../model';

export class Recipients extends Component {
  onRecipientsAdd() {
    this.props.dispatch(pushView('recipients/add'));
  }

  async onRecipientsDelete(recipient) {
    const result = await deleteRecipient(recipient.id);

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      window.alert('삭제되었습니다.');
      this.props.dispatch(aDeleteRecipient(recipient.id));
    }
  }

  onRecipientsUpdate(recipient) {
    this.props.dispatch(pushView('recipients/update', { recipientId: recipient.id }));
  }

  renderRecipient(recipient) {
    const isAgency = this.props.profile.id !== recipient.user_id;
    const {
      id, unit, accountname,
      bank, branch, account,
      phone1, phone2,
    } = recipient;
    let flagUrl;
    switch(unit) {
    case 'JPY':
      flagUrl = 'img-japan.svg';
      break;
    case 'CNY':
      flagUrl = 'img-china.svg';
      break;
    case 'VND':
      flagUrl = 'img-vietnam.svg';
      break;
    }

    return (
    <div key={ `recipient-${id}` } className='recipient-card'>
        <div className='pic'>
          <img src={ `${ IMG_URL }/${ flagUrl }` } />
          <p style={{ marginTop: '10px' }}>{ unit }</p>
        </div>
        <div className='details'>
          <h3>{ accountname }</h3>
          <div className='account moin-body'>
            <div className='moin-row'>
              <p>은행</p>
              <p>{ bank }</p>
            </div>
            { branch ?
              <div className='moin-row'>
                <p>지점</p>
                <p>{ branch }</p>
              </div> : null
            }
            <div className='moin-row'>
              <p>계좌</p>
              <p>{ account }</p>
            </div>
            { unit === 'CNY' ?
            <div className='moin-row'>
              <p>전화</p>
              <p>{ `${ phone1 } ${ phone2 }` }</p>
            </div> : null
            }
          </div>
        </div>
        <div className='spacer'></div>
        <div className='moin-inner buttons'>
          { isAgency ?
            null :
            <a className='btn-del'
                onClick={ this.onRecipientsDelete.bind(this, recipient) }>
              삭제
            </a>
          }
          <button className='btn-modify'
              onClick={ this.onRecipientsUpdate.bind(this, recipient) }>
            { isAgency ? '조회' : '수정' }
          </button>
        </div>
    </div>
    );
  }

  render() {
    return (
    <div className='themoin-recipients'>
    <div>
      <div className='recipients'>
        <h1>받는 분 목록</h1>
        <a className='recipients-add'
            onClick={ this.onRecipientsAdd.bind(this) }>
          + 받는 분 추가
        </a>
        {this.props.recipients
          .map(recipient => this.renderRecipient(recipient))
          .slice().reverse()
        }
      </div>
    </div>
    </div>
    );
  }
}

Recipients.propTypes = {
  dispatch: PropTypes.func,
  profile: PropTypes.object,
  recipients: PropTypes.array,
};

Recipients.mapState = state => ({
  profile: state.profile,
  recipients: state.recipients,
});

export default connect(Recipients.mapState)(Recipients);



