import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Recipients } from './Recipients';
import RemitFormRecipientsAdd from './RemitFormRecipientsAdd';
import {
  updateRemitRecipient,
  deleteRecipient as aDeleteRecipient,
} from '../action';
import { getEmptyString } from '../util';
import { IMG_URL, deleteRecipient } from '../model';

export class RemitFormRecipients extends Recipients {
  componentWillMount() {
    const props = this.props;
    const recipients = props.recipients.filter(recipient => recipient.unit === props.component.unit);
    let recipientId = null;

    if (recipients.indexOf(props.recipient) >= 0) {
      recipientId = props.recipients.indexOf(props.recipient);
    } else if (recipients.length === 1) {
      recipientId = props.recipients.indexOf(recipients[0]);
      props.dispatch(updateRemitRecipient(recipients[0]));
    }

    this.setState({
      isNew: recipients.length === 0,
      recipientId,
    });
  }

  componentWillReceiveProps(newProps) {
    if (newProps.vToken > this.props.vToken &&
        !this.state.isNew &&
        this.state.recipientId === null) {
      window.alert('받는 분을 선택해주세요.');
    }

    const newUnit = newProps.component.unit;
    if (this.props.component.unit !== newUnit) {
      const recipients = newProps.recipients.filter(recipient => recipient.unit === newUnit);
      let recipientId = null;

      if (recipients.length === 1) {
        recipientId = newProps.recipients.indexOf(recipients[0]);
        this.props.dispatch(updateRemitRecipient(recipients[0]));
      }

      this.setState({
        isNew: recipients.length === 0,
        recipientId,
      });
    }
  }

  onRecipientSelect(recipient) {
    const index = this.props.recipients.indexOf(recipient);

    recipient.isNew = false;
    if (this.state.recipientId !== null) return;

    this.props.dispatch(updateRemitRecipient(recipient));

    this.setState({ recipientId: recipient ? index : null });
  }

  onRecipientDeselect() {
    this.props.dispatch(updateRemitRecipient({}));
    this.setState({ recipientId: null });
  }

  onNew() { this.setState({ isNew: true }); }
  onEdit() { this.setState({ isEdit: true }); }

  async onDelete(recipient, e) {
    e.stopPropagation();

    if (!confirm(`받는 분 정보를 삭제합니다. [${ recipient.accountname }]`)) return;

    const result = await deleteRecipient(recipient.id);

    if (result.ret === 'failure') {
      window.alert(result.msg);
    } else {
      window.alert('삭제되었습니다.');
      this.props.dispatch(aDeleteRecipient(recipient.id));
    }
  }

  renderList() {
    const { recipientId } = this.state;
    const { unit } = this.props.component;
    const items = this.props.recipients
      .filter((_, index) => recipientId !== null ? index === recipientId : true)
      .filter(recipient => recipient.unit === unit)
      .map((recipient, index) => {
        let { phone1, phone2, zip, address, bank, branch, account } = recipient;
        if (!branch) branch = '';

        const fields = recipientId !== null ? [
          { label: '전화번호', value: `${phone1} ${phone2}` },
          { label: '우편번호', value: zip },
          { label: `주${ getEmptyString(9) }소`, value: address },
          { label: '수취은행', value: bank },
        ] : [
          { label: '수취은행', value: `${bank} ${branch}` },
        ];

        if (recipientId !== null && branch) {
          fields.push({ label: `지${ getEmptyString(9) }점`, value: branch });
        }

        fields.push(
          { label: '계좌번호', value: account }
        );

        return {
          recipient,
          fields,
        };
      }
    );

    return (
      <div className='list'>
        {items.map((item, index) => (
        <div key={ `recipient-${index}` } className='recipient-card'
            onClick={ this.onRecipientSelect.bind(this, item.recipient) }>
          <div className='moin-inner pic'>
            <img src={ `${ IMG_URL}/img-remittee-list.svg` } />
          </div>
          <div className='moin-inner details'>
            <h3 className='color-deepblue'>
              { item.recipient.accountname }
            </h3>
            <div className='account moin-body'>
              {item.fields.map((field, i) => (
                <div className='moin-row' key={ `recipient-field-${i}` }>
                  <p style={{ minWidth: '70px' }}>{ field.label }</p>
                  <p>{ field.value }</p>
                </div>
              ))}
            </div>
          </div>
          <div className='spacer'></div>
          { recipientId !== null ?
          <a className='edit' onClick={ this.onEdit.bind(this) }>
            <img src={ `${ IMG_URL }/edit.svg` } />
          </a> :
          <a className='delete' onClick={ this.onDelete.bind(this, item.recipient) }>
            <img src={ `${ IMG_URL }/delete.svg` } />
          </a>
          }
        </div>
          ))
        }
      </div>
    );
  }

  render() {
    const { recipients } = this.props;
    const { recipientId, isNew, isEdit } = this.state;

    if (isNew || isEdit || recipients.length === 0) {
      const recipient = isEdit ? recipients[recipientId] : undefined;

      return (
        <RemitFormRecipientsAdd vToken={ this.props.vToken }
          focusable={ this.props.focusable }
          recipient={ recipient }
          isUpdate={ isEdit }>
        </RemitFormRecipientsAdd>
      );
    }

    return (
    <div className='themoin-remit-form-recipients'>
      <h2 className='color-deepblue' style={{ paddingBottom: '20px', textAlign: 'center' }}>
        { recipientId != null ? '받는 분 정보를 확인해주세요.' : '받는 분을 선택해주세요.' }
      </h2>
      <p style={{ paddingBottom: '19px', textAlign: 'center'  }}>
        { recipientId != null ?
          '아래 연락처로 송금 진행상황이 안내됩니다. 중요한 안내를 받지 못하는 일이 없도록 한 번 더 확인해주세요.' :
          '아래는 최근에 송금 받은 분 목록입니다. 받으실 분을 선택해주세요.'
        }
      </p>
      { this.renderList() }
      { recipientId !== null ?
        <div className='sender-detail'
            style={{ marginTop: '22px' }}>
          <button className='btn-sender-new btn-load-recipientform' type='button'
              onClick={ this.onNew.bind(this) }>
            새로 등록하겠습니다.
          </button>
          <button className='btn-sender-list' type='button'
              onClick={ this.onRecipientDeselect.bind(this) }>
            최근 목록에서 고르겠습니다.
          </button>
        </div> :
        <div className='sender-simple'>
          <a onClick={ this.onNew.bind(this) }>
            새로 입력하시겠습니까?
          </a>
        </div>
      }
    </div>
    );
  }
}

RemitFormRecipients.propTypes = {
  dispatch: PropTypes.func,
  recipients: PropTypes.array,
  recipient: PropTypes.object,
  component: PropTypes.object,
  vToken: PropTypes.number,
  focusable: PropTypes.bool,
};

RemitFormRecipients.mapState = state => ({
  recipients: state.recipients,
  recipient: state.remit.recipient,
  component: state.remit.component,
});

export default connect(RemitFormRecipients.mapState)(RemitFormRecipients);



