import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

const DEFAULT_COLOR = '#a0a7b8';
const FOCUS_COLOR = '#292767';

/*
  vToken++ => run validator and if invalid => get focus
*/
export default class MoinTextInput extends Component {
  static get EmptyValidator() {
    return value => ({
      pass: value && value.length > 0,
      message: '값을 입력하여야 합니다.',
    });
  }

  static get KorEngValidator() {
    return function(value) {
      const regex = new RegExp('^[A-Za-z0-9,. ~!@\#$%<>^&*()-=+_+]*$', 'g');

      return {
        pass: regex.test(value),
        message: '영문과 숫자만 입력하여야 합니다.',
      };
    };
  }

  static get JpnValidator() {
    return function(value) {
      const regex = new RegExp('^[A-Za-z0-9,. ~!@\#$%<>^&*()-=+_+　０-９'
        + '\uFF00-\uFFEF'// 전각/반각 특수문자
        + '\u30A0-\u30FF'// 가타카나
        + '\u31F0-\u31FF'// 가타카나 음성 확장
        + '\u3000-\u303F'// 한중일 기호 및 구두점
        + '\u1B000-\u1B0FF'// 일본어 보충 문자
        + ']*$', 'g');

      return {
        pass: regex.test(value),
        message: '가타카나로만 입력하여야 합니다.',
      };
    };
  }

  static get ChnEngValidator() {
    return function(value) {
      const regex = new RegExp('^[A-Za-z0-9,. ~!@\#$%<>^&*()-=+_+'
        + '\u4E00-\u9FFF'// common
        + '\u3400-\u4DFF\uF900-\uFAFF'
        + '\u20000-\u2A6DF\u2A700-\u2B73F\u2B740-\u2B81F\u2B820-\u2CEAF\u2F800-\u2FA1F'// 한,중,일 호환한자
        + ']*$', 'g');

      return {
        pass: regex.test(value),
        message: '중국어 혹은 영어로만 입력하여야 합니다.',
      };
    };
  }

  static get PasswordValidator() {
    return function(value) {
      const mediumRegex = new RegExp('^(?=.{6,20})((?=.*[A-Z])|(?=.*[a-z])|(?=.*[0-9])|(?=.*[~!@\#$%<>^&*()-=+_])).*$', 'g');

      if (!value) return { pass: true };

      if (value.length < 6 || value.length > 20) {
        return {
          pass: false,
          message: '비밀번호는 6자 이상, 20자 이하여야 합니다.',
        };
      } else if (!mediumRegex.test(value)) {
        return {
          pass: false,
          message: '영문, 숫자, 특수문자만 입력하여야 합니다.',
        };
      }

      return { pass: true };
    }
  }

  constructor() {
    super();

    this.state = {
      color: DEFAULT_COLOR,
      errorMessage: null,
      tipActive: false,
    };

    this.$input = null; // reserved for input dom element
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.vToken > this.props.vToken) {
      const value = nextProps.value;
      const validators = this.getValidators(true);
      const valid = this.runValidators({ value, validators });

      this.props.onChange({ value: null, valid });
      this.setState({
        color: DEFAULT_COLOR,
        tipActive: false,
      });
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.vToken > prevProps.vToken &&
        this.state.errorMessage !== null) {
      this.$input.focus();
    }
  }

  getValidators(includePost = false) {
    let validators = this.props.validators ?
      this.props.validators : [];

    if (includePost && this.props.postValidators) {
      validators = validators.concat(this.props.postValidators);
    }

    return validators;
  }

  runValidators({ value, validators }) {
    let errorMessage = null;
    for (const validate of validators) {
      const { pass, message } = validate(value);
      if (!pass) {
        errorMessage = message;
        break;
      }
    }

    this.setState({ errorMessage });
    return errorMessage === null;
  }

  onFocus() {
    const tipActive = this.props.tip && this.state.errorMessage === null ? true : false;

    this.setState({
      color: FOCUS_COLOR,
      tipActive,
    });
  }

  onBlur() {
    const value = this.props.value;
    const validators = this.getValidators(true);
    const valid = this.runValidators({ value, validators });

    this.props.onChange({ value, valid });
    this.setState({
      color: DEFAULT_COLOR,
      tipActive: false,
    });

    if (this.props.onBlur) this.props.onBlur();
  }

  onChange(e) {
    const value = this.props.isNumeric ?
      e.target.value.replace(/[^0-9]/g, '') : e.target.value;

    const validators = this.getValidators(false);
    const valid = this.runValidators({ value, validators });

    this.props.onChange({ value, valid });
  }

  render() {
    const color = this.state.color;
    const inputClassName= classnames({
      'moin-input-error': this.props.errorMessage || this.state.errorMessage !== null,
    });
    const errorClassName = classnames('moin-error', {
      active: (
        this.props.errorMessage ||
        this.state.tipActive ||
        this.state.errorMessage !== null
      ),
    });

    let errorMessage;
    if (this.props.errorMessage) {
      errorMessage = this.props.errorMessage;
    } else if (this.state.tipActive) {
      errorMessage = this.props.tip;
    } else if (this.state.errorMessage !== null) {
      errorMessage = this.state.errorMessage;
    } else {
      errorMessage = null;
    }

    return (
      <div className={ this.props.className }>
        <div className='moin-input'>
          { this.props.label ?
            <label style={{ color }}>{ this.props.label }</label> :
            null
          }
          <input
            ref={$input => { this.$input = $input; }}
            className={ inputClassName }
            placeholder={ this.props.placeholder }
            value={ this.props.value }
            type={ this.props.type ? this.props.type : 'text' }
            tabIndex={ this.props.tabIndex }
            maxLength={ this.props.maxLength }
            onChange={ this.onChange.bind(this) }
            onFocus={ this.onFocus.bind(this) }
            onBlur={ this.onBlur.bind(this) } />
        </div>
        <p className={ errorClassName }>{ errorMessage }</p>
      </div>
    );
  }
}

MoinTextInput.defaultProps = {
  tabIndex: 0,
};

MoinTextInput.propTypes = {
  type: PropTypes.string,
  isNumeric: PropTypes.bool,
  value: PropTypes.string,
  placeholder: PropTypes.string,
  maxLength: PropTypes.string,
  tip: PropTypes.string,
  label: PropTypes.string,
  className: PropTypes.string,
  validators: PropTypes.array,
  postValidators: PropTypes.array,
  errorMessage: PropTypes.string,
  onChange: PropTypes.func,
  onBlur: PropTypes.func,
  tabIndex: PropTypes.number,
  vToken: PropTypes.number, // validatorToken changes -> run validator
};



