import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { pushView } from '../action';
import {
  IMG_URL,
  login,
  loginFacebook,
  loginNaver,
  resetPassword,
} from '../model';

import MoinTextInput from './components/MoinTextInput';

export class Login extends Component {
  static onFacebookLogin() { loginFacebook(); }
  static onNaverLogin() { loginNaver(); }

  constructor() {
    super();

    this.state = {
      email: '',
      password: '',
      emailErrorMessage: null,
      resetPopupHidden: true,
    };
  }

  async onLogin(e) {
    e.preventDefault();
    const { email, password } = this.state;
    this.passwordInput.onBlur();

    const result = login({ email, password });

    this.setState({
      emailErrorMessage: (result.ret === 'failure' ? result.msg : null),
    });
  }

  async onResetPassword(e) {
    e.preventDefault();

    resetPassword(this.state.email);
  }

  onChange(name, e) {
    const newState = {
      [name]: e.value,
    };

    this.setState(newState);
  }

  onSignUp() {
    this.props.dispatch(pushView('signup'));
  }

  onResetPopupOpen() { this.setState({ resetPopupHidden: false }); }
  onResetPopupClose() { this.setState({ resetPopupHidden: true }); }

  renderResetPasswordModal() {
    if (this.state.resetPopupHidden) return null;

    return (
    <div className='popup' onClick={ this.onResetPopupClose.bind(this) }>
      <div className='reset-password-popup' onClick={ e => e.stopPropagation() }>
        <h2>비밀번호 찾기</h2>
        <p>가입 시 입력한 이메일 주소로 임시 비밀번호를 보내드립니다.</p>
        <form action='/a/v1/member/forgotpassword' method='POST'
            onSubmit={ this.onResetPassword.bind(this) }>
          <MoinTextInput className='moin-input-group'
            label='이메일'
            placeholder='youremail@email.com'
            value={ this.state.email }
            onChange={ this.onChange.bind(this, 'email')}>
          </MoinTextInput>
          <input className='modal-btn back-deepblue' type='submit' value='임시 비밀번호 전송' />
          <a className='underline color-deepgrey'
                onClick={ this.onSignUp.bind(this) }>
              회원가입 하기
          </a>
          <a className='underline color-deepgrey'
              onClick={ this.onResetPopupClose.bind(this) }>
            로그인 하기
          </a>
        </form>
      </div>
    </div>
    );
  }

  render() {
    return (
    <div className='themoin-login'>
    <div>
      <div className='moin-login'>
        <div className='logo'>
          <img src={ `${ IMG_URL }/img-moin-logo.svg` } style={{ width: '170px' }} />
          <p className='modal-text'>안전하고 똑똑한 해외송금! MOIN과 함께하세요!</p>
        </div>
        <form id='moin-login' method='post'
            action='/a/v1/member/login'
            onSubmit={ this.onLogin.bind(this) }>
          <MoinTextInput className='moin-input-group'
            label='이메일'
            placeholder='youremail@email.com'
            value={ this.state.email }
            errorMessage={ this.state.emailErrorMessage }
            onChange={ this.onChange.bind(this, 'email')}>
          </MoinTextInput>
          <MoinTextInput className='moin-input-group'
            ref={(input) => { this.passwordInput = input; }}
            label='비밀번호'
            type='password'
            placeholder='비밀번호를 입력해주세요.'
            maxLength='20'
            value={ this.state.password }
            postValidators={[ MoinTextInput.PasswordValidator ]}
            onChange={ this.onChange.bind(this, 'password') }>
          </MoinTextInput>
          <button className='btn-login' type='submit'>
            로그인
          </button>
        </form>
        <p className='or'>or</p>
        <button className='btn-facebook' onClick={ Login.onFacebookLogin }>
          페이스북 계정으로 로그인
        </button>
        <button className='btn-naver' onClick={ Login.onNaverLogin }>
          네이버 계정으로 로그인
        </button>
        <p style={{ margin: '33px auto 0 auto' }}>
          <a className='underline' onClick={ this.onSignUp.bind(this) }>아직 회원이 아니신가요?</a>
        </p>
        <p style={{ margin: '20px auto 0 auto '}}>
          <a className='underline' onClick={ this.onResetPopupOpen.bind(this) }>
            비밀번호를 잊어버리셨나요?
          </a>
        </p>
      </div>
      { this.renderResetPasswordModal() }
    </div>
    </div>
    );
  }
}

Login.propTypes = {
  dispatch: PropTypes.func,
};

Login = connect(() => ({}))(Login);
export default Login;



