import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import moment from 'moment';
import RemitComponent from './RemitComponent';

export class CurrencyComponent extends Component {
  static fillTransformPolyfill(style) {
    style.msTransform = style.transform;
    style.WebkitTransform = style.transform;
  }

  static get feeRate() {
    return {
      CNY: (0.99 + 1.00 + 0.95 + 0.99) / 4,
      JPY: (0.98 + 0.97 + 0.95 + 0.96) / 4,
    };
  }

  constructor(props) {
    super(props);

    this.state = {
      items: [], // day, JPY, CNY,
      maxDict: {
        JPY: 0,
        CNY: 0,
      },
      minDict: {
        JPY: 0,
        CNY: 0,
      },
      containerWidth: 0,
    };

    this.onResize = () => {
      this.setState({ containerWidth: this.getContainerWidth() });
    };
  }

  componentDidMount() {
    this.loadCurrency();
    this.setState({ containerWidth: this.getContainerWidth() });
    window.addEventListener('resize', this.onResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onResize);
  }

  getContainerWidth() {
    return this.$container.getBoundingClientRect().width;
  }

  async loadCurrency() {
    const access_key = '70f3655421d3b5bdf773aeb0d34dc2a1';
    const currencies = 'KRW,JPY,CNY';
    const source = 'USD';
    const format = '1';
    const now = moment();
    const endDate = now.format('YYYY-MM-DD');
    const startDate = now.subtract(9, 'days').format('YYYY-MM-DD');
    const url = `https://apilayer.net/api/timeframe
      ?access_key=${access_key}
      &currencies=${currencies}
      &source=${source}
      &start_date=${startDate}
      &end_date=${endDate}
      &format=${format}`.replace(/\n|\s/g, '');

    const exchangeUrl = RemitComponent.getExchangeRateUrl();
    const [response, response2] = await Promise.all([fetch(url), fetch(exchangeUrl)]);
    const [
      { success, quotes }, { success: liveSuccess, quotes: liveQuotes },
    ] = await Promise.all([response.json(), response2.json()]);

    if (!success || !liveSuccess) {
      console.error('Failed to fetch currency info');
      return;
    }

    quotes[endDate] = liveQuotes;

    let [maxJPY, maxCNY, minJPY, minCNY] = [-Infinity, -Infinity, Infinity, Infinity];

    const items = Object.keys(quotes).map(date => {
      const day = date.split('-')[2];
      const { USDCNY, USDJPY, USDKRW } = quotes[date];
      const JPY = USDKRW / USDJPY * 100;
      const CNY = USDKRW / USDCNY;

      if (JPY > maxJPY) maxJPY = JPY;
      if (JPY < minJPY) minJPY = JPY;
      if (CNY > maxCNY) maxCNY = CNY;
      if (CNY < minCNY) minCNY = CNY;

      return {
        JPY,
        CNY,
        day,
      };
    });

    const maxDict = {
      JPY: maxJPY,
      CNY: maxCNY,
    };

    const minDict = {
      JPY: minJPY,
      CNY: minCNY,
    };

    items.forEach(item => {
      item.yDict = {
        JPY: this.calculateY({ unit: 'JPY', item, maxDict, minDict }),
        CNY: this.calculateY({ unit: 'CNY', item, maxDict, minDict }),
      };
    });

    this.setState({ items, maxDict, minDict });
  }

  calculateY({ unit, item, maxDict, minDict }) {
    const max = maxDict[unit];
    const min = minDict[unit];
    // Graph height is 80px
    const y = 80 * (item[unit] - min) / (max - min);

    return y;
  }

  renderLineBox(itemTuple, w) {
    const { unit } = this.props;
    const [item1, item2] = itemTuple;
    const y = item1.yDict[unit] + 3;
    const dy = item2.yDict[unit] - item1.yDict[unit];
    const r = Math.atan(dy / w) / Math.PI * 180;

    const moinStyle = {
      transform: `translate3d(0, -${y}px, 0) rotateZ(${-r}deg)`,
    };
    const bankStyle = {
      transform: `translate3d(0, -${y + 24.5}px, 0) rotateZ(${-r}deg)`,
    };

    // browser polyfill
    CurrencyComponent.fillTransformPolyfill(moinStyle);
    CurrencyComponent.fillTransformPolyfill(bankStyle);

    return (
    <div className='line-box' key={ item1.day }>
      <div className='line color-bank' style={ bankStyle }></div>
      <div className='line color-moin' style={ moinStyle }></div>
    </div>
    );
  }

  renderDotBox(item) {
    const { unit } = this.props;
    const { yDict } = item;
    const y = yDict[unit];

    const moinStyle = {
      transform: `translate3d(0, -${y}px, 0)`,
    };
    const bankStyle = {
      transform: `translate3d(0, -${y + 20}px, 0)`,
    };

    // browser polyfill
    CurrencyComponent.fillTransformPolyfill(moinStyle);
    CurrencyComponent.fillTransformPolyfill(bankStyle);

    const feeRate = CurrencyComponent.feeRate[unit];
    const amount = item[unit];

    return (
    <div key={ `item-${item.day}` } className='dot-box'>
      <div className='dot color-bank' style={ bankStyle }></div>
      <div className='dot color-moin' style={ moinStyle }></div>
      <div className='date'>{ item.day }</div>
      <div className='currency-tooltip'>
        <div className='bank'>
          <div>은행 평균</div>
          <div>{ (amount * (100 + feeRate) / 100).toFixed(2) }</div>
        </div>
        <div className='moin'>
          <div>모인 환율</div>
          <div>{ amount.toFixed(2) }</div>
        </div>
      </div>
    </div>
    );
  }

  render() {
    const { unit } = this.props;
    const { items, containerWidth } = this.state;
    const w = containerWidth / (items.length - 1);
    const itemTuples = [];

    for (let i = 0; i < items.length - 1; i++) {
      itemTuples.push([items[i], items[i + 1]]);
    }

    const amount = items.length > 0 ? items[items.length - 1][unit].toFixed(2) : '-';
    const currencyText = `${unit === 'JPY' ? 100 : 1} ${unit} = ${amount} KRW`;

    return (
    <div className='themoin-currency-component'>
    <div>
      <div className='graph-lines' ref={(dom) => { this.$container = dom; } }>
        { itemTuples.map(itemTuple => this.renderLineBox(itemTuple, w)) }
      </div>
      <div className='graph-dots'>
        { items.map(item => this.renderDotBox(item)) }
      </div>
      <div className='content'>
        <div className='title'>실시간 모인 환율 (= 매매기준율)</div>
        <div className='currency'>{ currencyText }</div>
      </div>
      <div className='legend'>
        <div className='bank-avg'>
          <div></div> 은행 평균
        </div>
        <div className='moin-avg'>
          <div></div> 모인 환율
        </div>
      </div>
    </div>
    </div>
    );
  }
}

CurrencyComponent.propTypes = {
  dispatch: PropTypes.func,
  unit: PropTypes.string,
};

CurrencyComponent.mapState = state => ({
  unit: state.remit.component.unit,
});

export default connect(CurrencyComponent.mapState)(CurrencyComponent);


