import React, { Component } from 'react';
import { IMG_URL } from '../model';

class Company extends Component {
  componentDidMount() {
    try {
      const mapOptions = {
        center: new naver.maps.LatLng(37.4958186, 127.0275665),
        zoom: 11,
        disableDoubleClickZoom: true,
        disableDoubleTapZoom: true,
        disableKineticPan: true,
        disableTwoFingerTapZoom: true,
        draggable: true,
        logoControl: false,
      };

      const map = new naver.maps.Map('company-map', mapOptions);
      new naver.maps.Marker({
        position: new naver.maps.LatLng(37.4958186, 127.0275665),
        map,
      });
    } catch (e) {
      console.log(e);
    }
  }

  render() {
    return (
    <div className='themoin-company'>
    <div className='company-slogan'>
      <div>
        <h1>고객 중심의 송금 서비스,</h1>
        <h1>모인이 시작합니다.</h1>
      </div>
    </div>
    <div className='divider'></div>
    <div className='company-intro'>
      <div>
        <img src={ `${ IMG_URL }/img-intro.png` }></img>
        <div className='spacer'></div>
        <div>
          <h2>모인 회사 소개</h2>
          <p style={{ paddingTop: '25px' }}>
            모인은 최적의 해외송금 솔루션을 제공하는 핀테크 회사입니다. 2016년 3월 법인 설립을 시작으로 합리적인 가격으로 빠르고 편리한 송금 서비스를 제공하고 있습니다.<br/>
            모인은 비효율을 혁신적으로 해결하고, 사람을 위한 가치를 창출하고자 하며, 무엇보다도 고객의 돈을 안전하게 보내는 것을 최우선으로 합니다.<br/>
            <br/>모인은 2016년 12월 기준, 일본/중국 송금 서비스를 제공하고 있으며, 동남아시아, 유럽 등을 목표로 서비스를 확장할 예정입니다. 앞으로도 모인은 해외송금 서비스에 대한 끊임없는 연구를 통해 고객들에게 더 편리한 서비스를 제공할 수 있도록 노력하겠습니다.
          </p>
        </div>
      </div>
    </div>
    <div className='company-mission'>
      <div>
        <img src={ `${ IMG_URL }/img-mission.png` }></img>
        <div className='spacer'></div>
        <div>
          <h2>모인의 미션</h2>
          <p style={{ paddingTop:'25px' }}>해외로 돈을 보낼 때 우리는 너무나도 당연하게 은행을 통해 송금을 했습니다. 수수료가 비싸고 송금
            하는데 오랜 시간이 걸린다는 것을 알면서도, 왜 은행을 통할 수 밖에 없었을까요? 그건 은행만이
            안전하게 돈을 보낼 수 있는 유일한 송금 수단이라고 생각했기 때문입니다. 그래서 우리는 기존의
            문제점을 개선하면서 은행의 보안 수준을 맞춘 송금 방법을 개발하고 싶다고 생각했습니다.</p>
          <h3>은행 중심이 아닌 사용자 중심의 송금서비스를 만들자</h3>
          <p>이것이 모인을 설립한 이유입니다. 우리는 모두가 안심하고 만족하는 서비스를 제공하도록 노력할
            것이며 해외 송금 시장을 더 나은 방향으로 변화시키려 합니다.</p>
        </div>
      </div>
    </div>
    <div className='company-slogan'>
      <div>
        <h1><span className='blue'>모인은 솔직하고</span> 투명합니다.</h1>
      </div>
    </div>
    <div className='divider'></div>
    <div className='company-slogan'>
      <div>
        <h2>모인 CI 소개</h2>
      </div>
    </div>
    <div className='company-ci'>
      <div>
        <img className='logo' src={ `${ IMG_URL }/img-grid-logo.png` }></img>
        <div className='spacer'></div>
        <div>
          <p>
            모인의 CI는 해외송금 시장을 더 나은 방향으로 이끌어가고자 하는 신념과 동시에,<br />
            고객들에게 투명한 서비스를 제공하고자 하는 철학을 담고 있습니다.<br />
            모인 CI의 ‘짙은 청색’과 ‘밝은 하늘색’이 만나는 부분에서 마치 투명한 셀로판지를 맞댄 것과
            같은 모습은 모인의 투명성을 상징하며, 모인의 굵고 기울어진 고딕 형태의 문자는 빠르게 나아가는
            모인의 진취성과 기술적 혁신을 담고 있습니다.
          </p>
          <p>
            <img className='color' src={ `${ IMG_URL }/img-dot-purple.svg` }></img>
            <span className='color-deepblue'>MOIN NAVY</span>
            &nbsp;&nbsp;신뢰할 수 있는 / 진중한 / 책임성 있는
          </p>
          <p>
            <img className='color' src={ `${ IMG_URL }/img-dot-skyblue.svg` }></img>
            <span className='color-skyblue'>MOIN SKY</span>
            &nbsp;&nbsp;밝은 / 미래 지향적인 / 혁신적인
          </p>
        </div>
      </div>
    </div>
    <div className='company-slogan'>
      <div>
        <h1><span className='blue'>모인은 언제나</span> 열려있습니다.</h1>
      </div>
    </div>
    <div className='divider'></div>
    <div className='company-map'>
      <div>
        <div id='company-map'></div>
      </div>
    </div>
    <div className='company-info'>
      <div>
        <div className='row'>
          <div className='img location'></div>
          <p className='info-label'>모인위치</p>
          <div className='info-divider'></div>
          <p>서울시 서초구 서초대로 78길 22 홍우2빌딩 613호</p>
        </div>
        <div className='row no-margin'>
          <div className='img phone'></div>
          <p className='info-label'>고객센터</p>
          <div className='info-divider'></div>
          <p>070-5089-2017 (평일 9:30 - 18:30, 점심시간 12:00 - 13:00, 주말/공휴일 휴무)</p>
        </div>
        <div className='row'>
          <div className='img'></div>
          <p className='info-label'>팩스번호</p>
          <div className='info-divider'></div>
          <p>0504-393-9687</p>
        </div>
        <div className='row'>
          <div className='img email'></div>
          <p className='info-label'>이 메 일</p>
          <div className='info-divider'></div>
          <p>info@themoin.com</p>
        </div>
      </div>
    </div>
    </div>
    );
  }
}

export default Company;



