import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import RemitComponent from './RemitComponent';
import CurrencyComponent from './CurrencyComponent';
import MainRemitItem from './components/MainRemitItem';
import { insertComma } from '../util';
import { fetchRemitList, fetchLimit } from '../model';
// import { pushView } from '../action';

class Main extends Component {
  constructor(props) {
    super(props);

    this.state = {
      remitsLoaded: false,
      remits: [],
      limit: 0,
    };
  }

  componentDidMount() {
    this.loadRemitList();
    this.loadLimit();
  }

  async loadLimit() {
    const result = await fetchLimit();

    if(result.ret === 'failure') {
      this.setState({ Error: result.msg });
    } else {
      this.setState({
        limit : result.data,
      });
    }
  }

  async loadRemitList() {
    const result = await fetchRemitList();

    if (result.ret === 'failure') {
      this.setState({ Error: result.msg });
    } else {
      this.setState({
        remitsLoaded: true,
        remits: result.data,
      });
    }
  }

  render() {
    return (
    <div className='themoin-main'>
      <CurrencyComponent></CurrencyComponent>
      <RemitComponent></RemitComponent>
      <div style={{padding: '60px 20px'}}>
        <div>
          <div className='user-subtitle'>송금내역</div>
        </div>
        <div className='userLimit'>
          <p>올해 누적 송금액&nbsp;:&nbsp;
            <span className='color-deepblue'>{ insertComma(this.state.limit*1) }</span>
            &nbsp;원&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;잔여&nbsp;한도&nbsp;:&nbsp;
            <span className={ this.state.limit > 20000000 ? 'color-red' : 'color-deepblue' }>{ insertComma(20000000-this.state.limit) }</span>
            &nbsp;원
          </p>
        </div>
        <div className='remits'>
          {this.state.remitsLoaded ?
            null :
            <div id='remit-list'>
              <div id='moin-loading'>
                <div style={{marginLeft: 10}} className='stick'/>
                <div className='stick'/>
                <div className='stick'/>
                <div className='stick'/>
                <div className='stick'/>
                <div className='stick'/>
                <h1>Loading...</h1>
              </div>
            </div>
          }
          {this.state.remitsLoaded && this.state.remits.length === 0 ?
            null :
            this.state.remits.map((remit, index) => (
              <MainRemitItem
                key={ `remit-${index}` }
                index={ this.state.remits.length - index }
                remit={ remit }
                onReloadItems={ this.loadRemitList.bind(this) }>
              </MainRemitItem>
            ))
          }
        </div>
      </div>
    </div>
    );
  }
}

Main.propTypes = {
  dispatch: PropTypes.func,
  profile: PropTypes.object,
};

Main.mapState = state => ({
  profile: state.profile,
});

Main = connect(Main.mapState)(Main);
export default Main;


