import './styles/index.scss';
import 'whatwg-fetch';
import 'babel-polyfill';
import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import { reduce } from './reducer';
import { setStore } from './model';
import App from './views/App';

const stateElement = document.querySelector('#preloaded-state');
const initState = stateElement ?
  JSON.parse(stateElement.textContent) :
  {
    initView: 'landing',
    view: 'landing',
  };

if (stateElement) {
  const _stateElement = document.getElementById('preloaded-state');
  stateElement.parentNode.removeChild(_stateElement);
}

const store = createStore(reduce, initState);
setStore(store);

render(
  <Provider store={ store }>
    <App initState={ initState }/>
  </Provider>,
  document.getElementById('root')
);



