import {
  login as aLogin,
  updateLoginStatus as aUpdateLoginStatus,
} from './action';
import { awaitPostMessage } from './bridge';

let store;
let platform;
export let IMG_URL;

export function setStore(_store) {
  store = _store;
  platform = store.getState().platform;
  IMG_URL = store.getState().assetUrl;
}

function getUrl(url) {
  // return `http://localhost:8301/${ url }`;
  return `https://devapi.themoin.com/${ url }`;
}

function getOptions(method = 'GET', body) {
  const options = {
    headers: { 'Content-Type': 'application/json' },
    credentials: 'include',
    method,
  };

  if (body) options.body = JSON.stringify(body);

  return options;
}

export async function fetchProfile() {
  const url = getUrl('a/v1/member/profile');
  const options = getOptions('POST');
  const response = await fetch(url, options);
  const { data: profile } = await response.json();

  if (profile && profile.id) {
    ga('set', 'userId', String(profile.id));
  }

  store.dispatch(aUpdateLoginStatus(profile));
}

