export async function login({ email, password }) {
  const url = getUrl('a/v1/member/login');
  const options = getOptions('POST', { email, password });
  const response = await fetch(url, options);
  const { result, data: profile } = await response.json();

  if (result.ret === 'success') {
    ga('set', 'userId', String(profile.id));
    store.dispatch(aLogin(profile));
  }

  return result;
}

export async function loginFacebook() {
  location.href = getUrl('a/v1/member/facebook');
}

