export async function logout() {
  const url = getUrl('a/v1/member/logout');
  const options = getOptions('POST');
  const response = await fetch(url, options);
  const { result } = await response.json();

  if (result.ret !== 'success') return;

  if (platform === 'web') {
    location.href = '/';
    return;
  }

  // React Native
  const sendMessage = {
    type: 'LOGOUT',
    result: true,
  };

  awaitPostMessage();
  window.postMessage(JSON.stringify(sendMessage), '*');
}

