import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class MoinPopup extends Component {
  renderPopup() {
    return;
  }

  render() {
    return (
      <div className='moin-popup' onClick={ this.props.closePopup }>
        { !this.props.hidden ? this.renderPopup() : null }
      </div>
    );
  }
}

MoinPopup.propTypes = {
  hidden: PropTypes.bool,
  closePopup: PropTypes.func,
};


