import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { pushView, updateRemitComponent, updateRemitRecipient } from '../action';
import { IMG_URL } from '../model';

class RemitSuccess extends Component {
  constructor(props) {
    super(props);

    const {unit, sendAmount } = props.component;
    this.state = { unit, sendAmount };
  }

  componentDidMount() {
    this.props.dispatch(updateRemitRecipient({}));
    this.props.dispatch(updateRemitComponent({
      unit: this.props.component.unit,
      sendAmount: '1,000,000',
      receiveAmount: '0',
      couponCode: '',
      couponAmount: 0,
    }));

    //10ping CPA code
    const apiRequestUrl = _tp_GetApiRequestUrl('2017032717330001');
    if (apiRequestUrl !== '') {
      document.write(`<img height="1" width="1" alt="" style="display:none" src="${apiRequestUrl}" />`);
    }
  }

  render() {
    const { unit, sendAmount } = this.state;
    const onMain = () => {
      this.props.dispatch(pushView('main'));
    };

    return (
    <div className='themoin-remit-success'>
    <div>
      <img src={ `${ IMG_URL }/img-success.svg` } />
      <h1>송금 신청이 완료되었습니다.</h1>
      <p className='intro'>
        모인 해외송금을 이용해주셔서 감사합니다.<br />
        신청 서류 확인 후 입금 계좌를 30분 이내에 문자로 안내해드립니다.<br />
        모인 영업시간 외에는 계좌 안내가 다소 늦어질 수 있습니다.<br />
        진행 상황은 메인화면의 송금 내역에서 확인하실 수 있습니다.<br />
      </p>
      <div className='box'>
        <p>입금할 금액</p>
        <h3>
          { `${sendAmount} KRW` }
        </h3>
        <p className='account'>입금 계좌</p>
        <h3>
          서류 확인 후 이메일과 문자로 안내해드립니다.<br />
          (평일 영업시간 기준 30분 이내)
        </h3>
      </div>
      { unit === 'CNY' ?
        <p className='process'>
          보내주신 돈은 <span>평일/주말 기준 최소 2시간~최대 24시간</span><br />
          이내에 송금 완료 됩니다. 또한 수취 계좌에는 모인의 파트너사인<br />
          <span>'北京利韬兴业科技有限公司 (북경리타오산업주식회사)'</span><br />
          <span>이름으로 입금</span>되오니 착오 없으시길 바랍니다.
        </p> :
        <p className='process'>
          보내주신 돈은 <span>일본 은행 영업시간(09:00 - 15:00, 주말 및 공휴일 제외)</span><br />
          기준으로 최대 48시간 이내에 송금 완료 됩니다.<br />
          또한 수취 계좌에는 모인의 파트너사인<br />
          <span>‘コインチェック (Coincheck)’ 이름으로 입금</span> 되오니 착오 없으시길 바랍니다.
        </p> 
      }
      <a onClick={ onMain }>메인화면으로</a>
    </div>
    </div>
    );
  }
}

RemitSuccess.propTypes = {
  dispatch: PropTypes.func,
  component: PropTypes.object,
};

RemitSuccess.mapState = (state) => ({
  component: state.remit.component,
});

export default connect(RemitSuccess.mapState)(RemitSuccess);


