import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { popView, POP_VIEW, PUSH_VIEW } from './action';

let isMounted = false;

class _History extends Component {
  static toggleChatRemit(view) {
    const $chat = document.querySelector('#moin-ch-plugin-root');
    const remitExists = $chat.classList.contains('remit');
    const hiddenExists = $chat.classList.contains('hidden');
    const isRemitPage = ['remit/01', 'remit/update'].includes(view);
    const isAgencyPage = /^agency\/\w+/.test(view);

    if (isRemitPage && !remitExists) {
      $chat.classList.add('remit');
    } else if (!isRemitPage && remitExists) {
      $chat.classList.remove('remit');
    }

    if (isAgencyPage && !hiddenExists) {
      $chat.classList.add('hidden');
    } else if (!isAgencyPage && hiddenExists) {
      $chat.classList.remove('hidden');
    }
  }

  static pushState({ action, state, prevView }) {
    if (!isMounted) return;

    let url;
    switch (state.view) {
    case 'landing':
      url = '/';
      break;
    case 'recipients/update':
      url = `/${state.view}/${state.recipientId}`;
      break;
    case 'remit/update':
      if(action.type === PUSH_VIEW) {
        url = `/${state.view}/${action.param.remit.remitId}`;
      }
      break;
    default:
      url = `/${state.view}`;
      break;
    }

    if (action.type !== POP_VIEW && state.view !== prevView) {
      _History.toggleChatRemit(state.view);

      window.history.pushState({ view: state.view }, state.view, url);
    }
  }

  componentDidMount() {
    const {
      initState,
      dispatch,
      view,
      platform,
    } = this.props;

    _History.toggleChatRemit(view);
    isMounted = true;

    if (platform !== 'web') return;

    window.onpopstate = event => {
      const view = event.state ?
        event.state.view : initState.initView;

      _History.toggleChatRemit(view);
      dispatch(popView(view));
    };
  }

  render() {
    return <div style={{ display: 'none' }}></div>;
  }
}

export const pushState = _History.pushState;

_History.propTypes = {
  initState: PropTypes.object,
  dispatch: PropTypes.func,
  view: PropTypes.string,
  platform: PropTypes.string,
};

_History.mapState = state => ({
  view: state.view,
  platform: state.platform,
});

export const History = connect(_History.mapState)(_History);



