import React from 'react';
import PropTypes from 'prop-types';
import MoinPopup from './MoinPopup';
import { IMG_URL } from '../../model';

export default class MoinPopupCoupon extends MoinPopup {
  constructor(props) {
    super(props);

    this.state = {
      couponCode: '',
    };
  }

  onPopupClick(e) {
    e.stopPropagation();
  }

  onTextChange(e) {
    const state = { couponCode : e.target.value };

    this.setState(state);
  }

  renderPopup() {
    return(
      <div className='coupon-popup'>
        <div className='popup-content' onClick = { this.onPopupClick.bind(this) }>
          <button type="button" style={{left: 483, top: 23}} className="popup-close"
                  onClick={ this.props.closePopup }>
            <img src={ `${ IMG_URL }/btn-close.png` } />
          </button>
          <input id="coupon-code" placeholder="쿠폰 코드를 입력해주세요." className="coupon-input"
                 onChange={ this.onTextChange.bind(this)}/>
          <button type="submit" style={{background: 'none'}} className="coupon-btn"
                  onClick={ this.props.setCoupon.bind(this, this.state.couponCode) }>
            입력
          </button>
        </div>
      </div>
    );
  }
}

MoinPopupCoupon.propTypes = {
  setCoupon: PropTypes.func,
};


