import { Component } from 'react';
import {
  login,
  mobileFacebookLogin,
  mobileNaverLogin,
  logout,
} from './model';

export function awaitPostMessage() {
  let isReactNativePostMessageReady = !!window.originalPostMessage;
  const queue = [];

  let currentPostMessageFn = function store(message) {
    if (queue.length > 100) queue.shift();
    queue.push(message);
  };

  if (!isReactNativePostMessageReady) {
    // const originalPostMessage = window.postMessage;
    Object.defineProperty(window, 'postMessage', {
      configurable: true,
      enumerable: true,
      get() {
        return currentPostMessageFn;
      },
      set(fn) {
        currentPostMessageFn = fn;
        isReactNativePostMessageReady = true;
        setTimeout(sendQueue, 0);
      },
    });
  }

  function sendQueue() {
    while (queue.length > 0) window.postMessage(queue.shift());
  }
}

const getAppMessage = function(e) {
  //const response = JSON.parse(e.data);

  const response = e.data;

  console.log('getAppMessage :: LOGIN >>>>>>>> ');

  switch (response.type) {
    case 'EMAIL_LOGIN':
      console.log('LOGIN');
      console.log(response.data);
      const result = login({ email: response.data.email, password: response.data.password });

      this.setState({
        emailErrorMessage: (result.ret === 'failure' ? result.msg : null),
      });
      break;
    case 'FACEBOOK_LOGIN':
      console.log('FACEBOOK_LOGIN');
      mobileFacebookLogin(response.data.token);
      break;
    case 'NAVER_LOGIN':
      console.log('NAVER_LOGIN');
      mobileNaverLogin(response.data.token);
      break;
    case 'LOGOUT':
      console.log('LOGOUT');
      logout();
      // window.location = 'ios:logout';
    break;
  }
};

export class Bridge extends Component {
  componentDidMount() {
    document.addEventListener('message', getAppMessage, false);

    window.getAppMessage = getAppMessage; 
  }

  render() { return null; }
}




