import { combineReducers } from 'redux';
import {
  PUSH_VIEW,
  POP_VIEW,
  LOGIN,
  LOGOUT,
  DELETE_RECIPIENT,
  UPDATE_REMIT_COMPONENT,
  UPDATE_REMIT_PROFILE,
  UPDATE_REMIT_PHOTO,
  UPDATE_REMIT_RECIPIENT,
  UPDATE_LOGIN_STATUS,
} from './action';
import { pushState } from './history';

// For handling first page history managing
function initView(state = 'landing', action) { return state; }

function view(state = 'remit/01', action) {
  if (state.length === 0) state = 'landing';

  switch (action.type) {
  case PUSH_VIEW:
    state = action.view;
    break;
  case POP_VIEW:
    state = action.view;
    break;
  case LOGIN:
    state = 'main';
    break;
  case LOGOUT:
    state = 'landing';
    break;
  }

  return state;
}

// loggedIn: null (init state) | true | false
function loggedIn(state = null, action) {
  switch (action.type) {
  case LOGIN:
    state = true;
    break;
  case LOGOUT:
    state = false;
    break;
  case UPDATE_LOGIN_STATUS:
    state = action.profile ? true : false;
    break;
  }
  return state;
}

const DEFAULT_PROFILE = {
  id: null,
  email: '',
  firstname: 'Tom',
  lastname: 'n Toms',
  phone1: '+82 (대한민국)',
  phone2: '',
  birth: '',
  country: '대한민국 (South Korea)',
  zip: '',
  address: '',
  subaddress: '',
  persontype: 1, // 1 내국인, 2 외국인?
  idcardUrl: null,
  profileUrl: null,
};

function profile(state = DEFAULT_PROFILE, action) {
  switch (action.type) {
  case LOGIN:
    state = action.profile;
    break;
  case PUSH_VIEW:
    if (action.param && action.param.profile) {
      state = action.param.profile;
    }
    break;
  case UPDATE_LOGIN_STATUS:
    if (action.profile) {
      state = action.profile;
    }
    break;
  }

  return state;
}

export const DEFAULT_REMIT = {
  component: {
    unit: 'JPY',
    sendAmount: '1,000,000',
    receiveAmount: '0',
    couponCode: '',
    couponAmount: 0,
    fee: 0,
    rate: 0,
  },
  profile: {},
  photo: {
    persontype: 1,
    idcardUrl: null,
    profileUrl: null,
  },
  recipient: {},
  remitId: null,
};

function remit(state = DEFAULT_REMIT, action) {
  let { component, profile, recipient, photo, remitId } = state;

  switch (action.type) {
    case '@@redux/INIT':
      if (!component) component = DEFAULT_REMIT.component;
      if (!profile) profile = DEFAULT_REMIT.profile;
      if (!photo) photo = DEFAULT_REMIT.photo;
      if (!recipient) recipient = DEFAULT_REMIT.recipient;
      if (!remitId) remitId = DEFAULT_REMIT.remitId;

      return { component, profile, photo, recipient, remitId };
    case UPDATE_REMIT_COMPONENT:
      return {
        component: action.component,
        profile,
        photo,
        recipient,
        remitId,
      };
    case UPDATE_REMIT_PROFILE:
      return {
        profile: action.profile,
        component,
        photo,
        recipient,
        remitId,
      };
    case UPDATE_REMIT_PHOTO:
      return {
        photo: action.photo,
        component,
        profile,
        recipient,
        remitId,
      };
    case UPDATE_REMIT_RECIPIENT:
      return {
        recipient: action.recipient,
        component,
        photo,
        profile,
        remitId,
      };
    case PUSH_VIEW:
      if (action.view === 'remit/update') {
        return {
          recipient: action.param.remit.recipient,
          component: action.param.remit.component,
          photo: action.param.remit.photo,
          profile: action.param.remit.profile,
          remitId: action.param.remit.remitId,
        };
      }
  }

  return state;
}

