function recipients(state = [], action) {
  switch (action.type) {
  case LOGIN:
    state = action.profile.recipients;
    break;
  case UPDATE_LOGIN_STATUS:
    if (action.profile) {
      state = action.profile.recipients;
    }

    break;
  case DELETE_RECIPIENT:
    state = state.filter(recipient => recipient.id !== action.recipientId);
    break;
  case PUSH_VIEW:
    if (action.param && action.param.recipients) {
      state = action.param.recipients;
    }
    break;
  }

  return state;
}

function recipientId(state = null, action) {
  if (action.type === PUSH_VIEW && action.view === 'recipients/update') {
    state = action.param.recipientId;
  }

  return state;
}

function platform(state = 'web', action) {
  return state;
}

function assetUrl(state = 'https://img.themoin.com/public/img', action) {
  return state;
}

const _reduce = combineReducers({
  nextView: (state = null) => state,
  initView,
  view,
  loggedIn,
  profile,
  remit,
  recipients,
  recipientId, // selected recipient
  platform, // web, mobile
  assetUrl,
});

const LOGIN_REQUIRED_VIEWS = [
  'main', 'account', 'recipients',
  'recipients/update', 'recipients/add',
  'remit/01', 'remit/02', 'remit/update',
  'pending',
];

export const reduce = (...params) => {
  const prevView = params[0].view;
  const state = _reduce(...params);
  const action = params[1];

  if ([LOGIN, UPDATE_LOGIN_STATUS].includes(action.type) &&
      (state.view === 'landing' || state.nextView) &&
      state.loggedIn) {
    // Redirect after login!
    state.view = state.nextView ? state.nextView : 'main';
    state.nextView = null;
  } else if (!state.loggedIn && LOGIN_REQUIRED_VIEWS.includes(state.view)) {
    if (state.view !== 'pending') {
      state.nextView = state.view;
    }

    state.view = state.loggedIn === null ? 'pending' : 'login';
  }

  if (state.platform === 'web') {
    pushState({ action, state, prevView });
  }

  return state;
};



