import React, { Component } from 'react';
import MoinTextInput from '../components/MoinTextInput';
import LandingStep from '../components/LandingStep';
import Landing from '../Landing';
import { IMG_URL, sendVerificationEmail } from '../../model';

export default class EventUnivJPY extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
    };
  }

  renderTitle() {
    return (
    <div className='section-title'>
      <img src={ `${ IMG_URL }/img-moin-logo.svg` } />
      <h1 className='pre-title'>{ '{ 일본 제휴대학 학생만의 특별한 혜택 }' }</h1>
      <h1>모인 해외송금 수수료를 할인해드립니다!</h1>
    </div>
    );
  }

  renderIntro() {
    return (
    <div className='section-intro'>
    <div>
      <h1>학교 이메일 인증만 하면 <br className='mobile-line-break' />해외송금 수수료가 낮아집니다!</h1>
      <p>
        모인과 제휴한 일본대학의 이메일만 인증하면, 모인 일본 송금 수수료가 1.3%로 낮아집니다.<br className='desktop-line-break' />
        은행과 수수료를 비교해보시고, 여러분을 위한 특별한 혜택을 놓치지 마세요!
      </p>
      <div className='intro-vs'>
        <img src={ `${ IMG_URL }/img-fee-moin.png` } />
        <div className='label-moin-normal'>1.5%</div>
        <img src={ `${ IMG_URL }/ic-arrow.svg` } />
        <div className='label-moin-event'>1.3%</div>
        <img src={ `${ IMG_URL }/img-fee-event.png` } />
      </div>
    </div>
    </div>
    );
  }

  renderHowTo() {
    return (
    <div className='section-howto'>
    <div>
      <h1>어떻게 참여하나요?</h1>
      <img className='desktop' src={ `${ IMG_URL }/img-event-step.svg` } />
      <div className='mobile'>
        <img src={ `${ IMG_URL }/img-event-step-1.svg` } />
        <p>이벤트 화면에서 학교 이메일을 입력하고 전송 버튼 클릭</p>
        <img src={ `${ IMG_URL }/img-event-step-2.svg` } />
        <p>인증 메일을 열어 인증하기 링크 클릭</p>
        <img src={ `${ IMG_URL }/img-event-step-3.svg` } />
        <p>모인 계정이 있으면 로그인, 계정이 없으면 회원가입!</p>
        <img src={ `${ IMG_URL }/img-event-step-4.svg` } />
        <p>인증 완료! 할인된 수수료로 송금하기만 하면 끝!</p>
      </div>
    </div>
    </div>
    );
  }

  onEmailChange(e) {
    const { value } = e;
    this.setState({ email: value });
  }

  async onClickSend(e) {
    e.preventDefault();

    const result = await sendVerificationEmail(this.state.email);
    window.alert(result.msg);
  }

  renderVerifyEmail() {
    return (
    <div className='section-verifyemail'>
    <div>
      <h1>학교 이메일 인증하고 수수료 할인받기</h1>
      <form onSubmit = { this.onClickSend.bind(this) }>
        <MoinTextInput className='input-email'
          placeholder='인증할 학교 이메일을 입력해주세요.'
          value={ this.state.email }
          vToken={ 1 }
          tabIndex={ 0 }
          postValidators={[ MoinTextInput.EmptyValidator ]}
          onChange={ this.onEmailChange.bind(this) }>
        </MoinTextInput>
        <input type='submit' value='인증 메일 전송' />
      </form>
    </div>
    </div>
    );
  }

  renderCompare() {
    return (
    <div className='section-compare'>
      <h1>100만원을 일본으로 송금할 경우 수수료 비교!</h1>
      <img className='desktop' src={ `${ IMG_URL }/table.svg` } />
      <img className='mobile' src={ `${ IMG_URL }/table-mobile.svg` } />
    </div>
    );
  }

  renderFAQ() {
    return (
    <div className='section-faq'>
    <div className='item-container'>
      <div className='item'>
        <h1>모인 해외송금이 궁금해요.</h1>
        <p>
          모인은 좀 더 빠르고 저렴하게 해외로 돈을 보낼 수 있는 온라인 해외송금 서비스입니다.
          기존에 비싸고 느렸던 해외송금 과정의 단점을 모인의 기술로 개선하여 은행 대비 최대 80% 저렴한 수수료로,
          보다 빠르게 송금이 가능합니다. 모인은 현재 대한민국에서 일본/중국으로의 송금 서비스를 제공하고 있습니다.
        </p>
      </div>
      <div className='item'>
        <img className='desktop' src={ `${ IMG_URL }/img-3-merits.svg` } />
        <img className='mobile' src={ `${ IMG_URL }/img-3-merits-m.svg` } />
      </div>
    </div>
    <div className='item-container'>
      <div className='item'>
        <h1>왜 유학생이 이용하면 좋은가요?</h1>
        <p>
          유학생이 일본에 가자마자 바로 계좌 개설이 가능한 곳은 ‘유초은행(우체국은행)’인데요,
          유초은행 계좌로 JASSO 장학금 등을 받을 수 있고, 학비를 낼 수 있으며, 계좌 개설이 타 은행에 비해 간편하다는 장점이 있습니다.
          그러나 유초은행도 일본 체류기간 6개월이 지나야 해외송금이 가능하다는 단점이 있습니다.
          모인으로 송금하시면 <span className='blue'>체류기간이 단 하루여도, 한국에서 유초은행 계좌로 바로 송금이 가능</span>합니다.
          모인은 SWIFT망을 지나지 않기 때문입니다.
        </p>
      </div>
      <div className='item'>
        <h1>모인은 왜 수수료가 저렴한가요?</h1>
        <p>
          기존 은행이 해외송금할 때 사용하는 SWIFT망 때문입니다.
          SWIFT망은 중계은행을 거쳐 송금하는 구조이기 때문에 국내은행 - 중계은행 - 수취은행에서 수수료 발생은 물론 시간도 걸립니다.
          하지만 <span className='blue'>모인은 SWIFT망을 거치지 않아, 시간 단축은 물론 수수료 역시 매우 저렴</span>합니다.
          게다가 모인은 온전히 송금 서비스에 집중하기 위해 환전수수료 역시 일체 받고 있지 않습니다.
          즉, 모인으로 송금하는 모든 분들이 환전우대 100%로 받으실 수 있습니다.
        </p>
      </div>
    </div>
    </div>
    );
  }

  renderProcess() {
    return (
    <div className='section-process themoin-landing'>
    <div className='process'>
      <h1>전체적인 송금 과정은 어떻게 진행되나요?</h1>
      <div>
        { Landing.renderProcess(true) }
      </div>
    </div>
    </div>
    );
  }

  renderStep() {
    return (
    <div className='section-step'>
    <div>
      <h1>모인 해외송금 홈페이지 이용 방법</h1>
      <LandingStep></LandingStep>
    </div>
    </div>
    );
  }

  render() {
    return (
    <div className='themoin-event-univ-jpy'>
    { this.renderTitle() }
    { this.renderIntro() }
    { this.renderHowTo() }
    { this.renderVerifyEmail() }
    { this.renderCompare() }
    { this.renderFAQ() }
    { this.renderProcess() }
    { this.renderStep() }
    { this.renderVerifyEmail() }
    </div>
    );
  }
}



