import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { IMG_URL } from '../../model';

export default class MoinDropdown extends Component {
  constructor(props) {
    super(props);

    this.state = {
      open: false,
      selectedIndex: props.selectedIndex !== undefined ? props.selectedIndex : 0,
    };
  }

  onOpen() {
    this.setState({ open: !this.state.open });
  }

  onItemSelect(index) {
    this.setState({
      open: false,
      selectedIndex: index,
    });

    if (this.props.onChange) {
      this.props.onChange(this.props.items[index].value);
    }
  }

  render() {
    const { label, items } = this.props;
    const className = classnames('moin-dropdown', { open: this.state.open });
    const selectedItem = items[this.state.selectedIndex];

    return (
    <div className={ className }>
      <label>{ label }</label>
      <a id='recipient-unit' className='dropdown-toggle'
          onClick={ this.onOpen.bind(this) }>
        { selectedItem.title ? selectedItem.title : selectedItem.value }
        <img className='pull-right' src={ `${ IMG_URL }/ic-dropdown-p.png` } />
      </a>
      <ul className='dropdown-menu' >
        {items.map((item, index) => (
          <li key={ item.value }>
            <a onClick={ this.onItemSelect.bind(this, index) }>
              { item.title ? item.title : item.value }
            </a>
          </li>
        ))}
      </ul>
    </div>
    );
  }
}

MoinDropdown.propTypes = {
  selectedIndex: PropTypes.number,
  label: PropTypes.string,
  items: PropTypes.array, // array of { ?title, value }
  onChange: PropTypes.func,
};



