import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Account } from './Account';
import { updateRemitProfile } from '../action';

export class RemitFormAccount extends Account {
  constructor(props) {
    super(props);

    this.state.editMode = false;
  }

  componentDidMount() { if(!this.props.isUpdate) this.updateRemitProfile(); }
  componentDidUpdate() { this.updateRemitProfile(); }

  componentWillReceiveProps(nextProps) {
    const vTokenGap = nextProps.vToken - this.props.vToken;
    if (vTokenGap > 0) {
      this.setState({
        vToken: this.state.vToken + vTokenGap,
      });
    }
  }

  updateRemitProfile() {
    // const profile = Object.assign({}, this.state.profile());
    // updateRemitProfile always force remit ref change
    this.props.dispatch(updateRemitProfile(this.state.profile));
  }

  hasSaved() {
    if (this.state.editMode) return false;

    return true;
    /*
    const {
      firstname, lastname, phone1, phone2, country, zip, address, subaddress,
    } = this.state.profile;

    return firstname && lastname && phone1 && phone2 && country && zip && addresss && subaddress;
    */
  }

  onEditMode() {
    this.setState({ editMode: true });
  }

  render() {
    return (
      <div className='themoin-account'>
      <div>
        <h2 style={{ textAlign: 'center' }}>보내는 사람의 정보를 입력해 주세요.</h2>
        <p style={{ marginTop: '10px', textAlign: 'center' }}>
          기입한 연락처로 송금 관련 문자가 발송됩니다. 중요한 안내를 받지 못하는 일이 없도록 정확히 입력해주세요.
        </p>
        { this.renderContent(true) }
      </div>
      </div>
    );
  }
}

RemitFormAccount.propTypes = {
  dispatch: PropTypes.func,
  profile: PropTypes.object,
  vToken: PropTypes.number,
  focusable: PropTypes.bool,
};

export default connect(RemitFormAccount.mapState)(RemitFormAccount);



