import React, { Component } from 'react';
import classnames from 'classnames';
import moment from 'moment';
import { insertComma } from '../util';
import { IMG_URL } from '../model';

const MONTHS = [...Array(12).keys()].map(i => ({ value: i + 1, content: `${i + 1}월` }));
const NUM_PAGE_ROWS = 15;

export default class Agency extends Component {
  static getRemitFilter(text) {
    return function (remit) {
      const regex = new RegExp(text, 'i');

      return (
        regex.test(remit.sender.firstname) ||
        regex.test(remit.sender.lastname) ||
        regex.test(remit.id) ||
        regex.test(remit.user_id) ||
        regex.test(remit.sender.phone2) ||
        regex.test(remit.create_time)
      );
    };
  }

  constructor(props) {
    super(props);

    const now = moment();
    const year = now.format('YYYY');
    const month = now.format('MM');

    this.state = {
      agencyName: '',
      inputText: '',
      amount: 0, // month
      totalAmount: 0, // all
      remits: [],
      page: 0,
      maxPage: 1,
      searchingText: '',
      inputYear: year,
      inputMonth: month,
      year,
      month,
    };
  }

  componentWillMount() {
    this.loadAgencyInfo();
    this.loadAgencyRemits();
  }

  pushView(view) {
    if (view === 'logout') {
      location.href='/logout';
      return;
    }

    console.log(view);
    location.href = view;
  }

  async loadAgencyInfo() {
    const response = await fetch('/a/v2/agency/info', {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' },
      credentials: 'same-origin',
    });

    const { result, agency } = await response.json();

    if (result.ret === 'failure') {
      window.alert(result.msg);
      return;
    }

    this.setState({ agencyName: agency.name });
  }

  async loadAgencyRemits() {
    const { year, month } = this.state;
    const response = await fetch('/a/v2/agency/remits', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      credentials: 'same-origin',
      body: JSON.stringify({ year, month }),
    });

    const { result, remits } = await response.json();

    if (result.ret === 'failure') {
      window.alert(result.msg);
      return;
    }

    const amount = remits.reduce((_amount, remit) => {
      if (!remit.agency_direct || remit.agency_confirmed) {
        _amount += remit.agency_fee;
      }

      return _amount;
    }, 0);

    this.setState({
      inputText: '',
      searchingText: '',
      maxPage: Math.ceil(remits.length / NUM_PAGE_ROWS),
      page: 0,
      amount,
      remits,
    });
  }

  async onAgencyRemitConfirm(remitId) {
    const response = await fetch('/a/v2/agency/remit/confirm', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      credentials: 'same-origin',
      body: JSON.stringify({ remitId }),
    });

    const { result } = await response.json();
    if (result.ret === 'failure') {
      window.alert(result.msg);
      return;
    }

    this.loadAgencyRemits();
  }

  onDateSearch() {
    const { inputYear, inputMonth } = this.state;

    this.setState({
      year: inputYear,
      month: moment(`${ inputMonth }`, 'M').format('MM'),
    }, () => {
      this.loadAgencyRemits();
    });
  }

  onTextSearch(e) {
    e.preventDefault();

    const { inputText, remits } = this.state;
    const searchingText = inputText;
    const filterFunction = Agency.getRemitFilter(searchingText);
    const maxPage = Math.ceil(remits.filter(filterFunction).length / NUM_PAGE_ROWS);

    this.setState({ maxPage, searchingText });
  }

  onTextChange(e) { this.setState({ inputText: e.target.value }); }
  onMonthChange(e) { this.setState({ inputMonth: e.target.value }); }
  onYearChange(e) { this.setState({ inputYear: e.target.value }); }
  onFirstPage() { this.onPage(0); }
  onPrevPage() { this.onPage(this.state.page - 1); }
  onNextPage() { this.onPage(this.state.page + 1)}
  onLastPage() { this.onPage(Infinity); }
  onPage(page) {
    const { maxPage } = this.state;

    if (page < 0) {
      page = 0;
    } else if (page >= maxPage) {
      page = maxPage - 1;
    }

    this.setState({ page });
  }


  renderHeader() {
    const { agencyName } = this.state;

    return (
    <nav className='header'>
      <a className='logo'
          onClick={ this.pushView.bind(this, '/main')}>
        <img src={ `${ IMG_URL }/img-moin-logo.svg` } />
      </a>
      <div className='title'>
        관리자 - { agencyName }
      </div>
      <div className='spacer'></div>
      <div className='total-amount'>
        총 누적 적립 금액 : <span className='money'>71,482</span> 원
      </div>
      <div className='sub-title'>송금 관리</div>
      <a onClick={ this.pushView.bind(this, 'logout') }>로그아웃</a>
    </nav>
    );
  }

  render() {
    const {
      amount, year, month, inputYear, inputMonth, remits,
      page, maxPage, inputText, searchingText,
    } = this.state;
    const end = moment(`${ year }-${ month }-01`, 'YYYY-MM-DD').add(1, 'month').subtract(1, 'day');
    const [endMonth, endDay] = [end.format('MM'), end.format('DD')];

    const pages = [];
    for (let i = 0; i < maxPage; i++) {
      pages.push(
        <a key={ `page-${i}` }
            className={ classnames('page', { current: i === page }) }
            onClick={ this.onPage.bind(this, i) }>
          { i + 1 }
        </a>
      );
    }

    pages.splice(0, 0, [
      <a key='page-first' className='first' onClick={ this.onFirstPage.bind(this) }></a>,
      <a key='page-prev' className='prev' onClick={ this.onPrevPage.bind(this) }></a>,
    ]);

    pages.splice(pages.length, 0, [
      <a key='page-next' className='next' onClick={ this.onNextPage.bind(this) }></a>,
      <a key='page-last' className='last' onClick={ this.onLastPage.bind(this) }></a>,
    ]);

    return (
      <div className='themoin-agency'>
        { this.renderHeader() }
        <p className='label-duration'>{ `조회 기준 기간 : ${ year }.${ month }.01~${ year }.${ endMonth }.${ endDay }` }</p>
        <h1>{ month }월 적립 금액은 { insertComma(amount) }원 입니다.</h1>
        <div className='date-search'>
          <select className='select-year'
              value={ Number(inputYear) }
              onChange={ this.onYearChange.bind(this) }>
            <option value='2017'>2017년</option>
          </select>
          <select className='select-month'
              value={ Number(inputMonth) }
              onChange={ this.onMonthChange.bind(this) }>
            {MONTHS.map(item => (
              <option key={ item.value } value={ item.value }>
                { item.content }
              </option>
            ))}
          </select>
          <a className='button' onClick={ this.onDateSearch.bind(this) }>
            검색
          </a>
        </div>
        <div className='list'>
          <table>
            <thead>
              <tr>
                <th>번호</th>
                <th>유저ID</th>
                <th>보내는 분</th>
                <th>보내는 금액</th>
                <th>받는 금액</th>
                <th>보내는 분 연락처</th>
                <th>신청일</th>
                <th>적립 금액</th>
                <th>처리 상태</th>
              </tr>
            </thead>
            <tbody>
              {remits
                  .filter(Agency.getRemitFilter(searchingText))
                  .filter((remit, index) => (index >= page * NUM_PAGE_ROWS && index < (page + 1) * NUM_PAGE_ROWS))
                  .map(remit => {
                const pending = remit.agency_direct && !remit.agency_confirmed;

                return (
                  <tr key={ remit.id }>
                    <td>{ remit.id }</td>
                    <td>{ remit.user_id }</td>
                    <td>{ `${remit.sender.firstname} ${remit.sender.lastname}` }</td>
                    <td>{ `${remit.send_amount} ${remit.send_unit}` }</td>
                    <td>{ `${remit.receive_amount} ${remit.receive_unit}` }</td>
                    <td>{ `${remit.sender.phone1} ${remit.sender.phone2}` }</td>
                    <td>{ `${ moment(remit.create_time).format('YYYY-MM-DD HH:mm') }` }</td>
                    <td className={ pending ? 'pending' : '' }>{ `+${ insertComma(remit.agency_fee) }` }</td>
                    <td>
                      { pending ?
                        <a className='button'
                            onClick={ this.onAgencyRemitConfirm.bind(this, remit.id) }>
                          처리
                        </a> :
                        (remit.agency_direct ?
                          <a className='button complete'>완료</a> :
                          null
                        )
                      }
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        <div className='footer'>
          <div className='spacer'></div>
          <div className='pagination'>
            { pages }
          </div>
          <form className='spacer remit-search' onSubmit={ this.onTextSearch.bind(this) }>
            <input type='text' value={ inputText } placeholder='검색해주세요...' 
              onChange={ this.onTextChange.bind(this) } />
            <input type='submit' className='button' value='검색' />
          </form>
        </div>
      </div>
    );
  }
}

Agency.propTypes = {};


