import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { updateRemitPhoto } from '../action';
import { IMG_URL, uploadRemitPhoto } from '../model';

class RemitFormPhotoUpload extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    if(!this.props.isUpdate) {
      const photo = {
        persontype: 1,
        profileUrl: this.props.profile.profile,
        idcardUrl: this.props.profile.idcard,
      };
      this.props.dispatch(updateRemitPhoto(photo));
    }
  }

  async uploadImage(file, type) {
    let data = new FormData();
    data.append('file', file);

    const { response, files } = await uploadRemitPhoto(data);

    if (!response.ok) {
      console.log(response.statusText);
      alert('사진 업로드에 실패하였습니다. 다시 시도해주세요.');
    } else {
      //this.props.onPhotoChange(type, { value: files[0].profileUrl });
      const photo = Object.assign({}, this.props.photo);
      photo[type] = files[0].profileUrl;

      this.props.dispatch(updateRemitPhoto(photo));
    }
  }

  onImageChange(e, input) {
    let file = e.target.files[0];

    this.uploadImage(file, input);
  }

  render() {
    const tabIndex = this.props.focusable ? 0 : -1;
    const { persontype, profileUrl, idcardUrl } = this.props.photo;

    return (
    <div className='RemitFormPhotoUpload themoin-container'>
      <h3 style={{ textAlign: 'center' }}>
        포토 ID를 통한 본인 인증
      </h3>
      <p style={{ marginTop: '15px' }}>
        포토 ID란 주민등록증이나 여권 사진 등 본인의 얼굴 사진이 포함된 신분 증명서입니다. 해당 절차는 비대면
        금융거래시 필요한 법적 필수사항에 근거한 것이며, 본인을 인증하는 것 이외에 다른 용도로 활용되지 않습니다.
      </p>
      <div style={{ textAlign: 'center' }}>
        <input type='radio' name='update-persontype' id='radio1'
          tabIndex={ tabIndex }
          value='1' defaultChecked={ persontype === 1 || persontype === 0 } />
        <label htmlFor='radio1' style={{'marginLeft': 5}}>
          <p>
            <span></span>
            내국인
          </p>
        </label>

        <input type='radio' name='update-persontype' id='radio2' style={{'marginLeft': 20}}
          tabIndex={ tabIndex }
          value='2' defaultChecked={ persontype === 2 } />
        <label htmlFor='radio2' style={{'marginLeft': 5}}>
          <p>
            <span></span>
            외국인
          </p>
        </label>
      </div>
      <div className='row'>
        <div>
          <div className='remit-pic-subtitle pull-left'>사진 1</div>
          <p className='pull-left' style={{ margin: '25px 0 0 11px' }}>신분증 사진 등록</p>
          <div className='clearfix'></div>
          <div className='remit-pic'
            style={{ backgroundImage: idcardUrl ?
              'url("' + idcardUrl + '")' :
              `url("${ IMG_URL }/example-photoid.jpg")` }}>
          </div>
          <div className='moin-btn-file'>
            사진 등록하기
            <input className='upload-pic' type='file' accept='image/*'
                   tabIndex={ tabIndex }
                   onChange={ (e)=>this.onImageChange(e, 'idcardUrl') }/>
          </div>
        </div>
        <div>
          <div className='remit-pic-subtitle pull-left'>사진 2</div>
          <p className='pull-left' style={{ margin: '25px 0 0 11px' }}>
            신분증과 함께 찍은 본인 사진
          </p>
          <div className='clearfix'></div>
          <div className='remit-pic'
             style={{ backgroundImage: profileUrl ?
                 'url("' + profileUrl + '")' :
                 `url("${ IMG_URL }/example-face.jpg")` }}>
          </div>
          <div className='moin-btn-file'>
            사진 등록하기
            <input className='upload-pic' type='file' accept='image/*'
                   tabIndex={ tabIndex }
                   onChange={ (e)=>this.onImageChange(e, 'profileUrl') } />
          </div>
        </div>
      </div>
    </div>
    );
  }
}

RemitFormPhotoUpload.propTypes = {
  dispatch: PropTypes.func,
  photo: PropTypes.object,
  profile: PropTypes.object,
  isUpdate: PropTypes.bool,
  focusable: PropTypes.bool,
};

const mapState = state => ({
  photo: state.remit.photo,
  profile: state.profile,
});

export default connect(mapState)(RemitFormPhotoUpload);


