import { connect } from 'react-redux';
import { RemitFormAccount } from './RemitFormAccount';

class RemitUpdateAccount extends RemitFormAccount {}

RemitUpdateAccount.mapState = (state) => ({
  profile: state.remit.profile,
});

export default connect(RemitUpdateAccount.mapState)(RemitUpdateAccount);



