import React, { Component } from 'react';
import { IMG_URL } from '../../model';

export default class LandingStep extends Component {
  static get ITEMS() {
    return [
      {
        title: '1. 회원가입 / 로그인',
        text: '이메일 주소로 회원가입 / 로그인 후 송금하기 버튼을 누릅니다.',
        src: 'img-step-1.svg',
      }, {
        title: '2. 보내는 금액 입력',
        text: '보내는 금액을 입력합니다. 쿠폰이 있다면 등록합니다.',
        src: 'img-step-2.svg',
      }, {
        title: '3. 보내는 사람 정보 입력',
        text: '내 정보를 입력하고 본인인증을 위해 신분증 사진을 등록합니다.',
        src: 'img-step-3.svg',
      }, {
        title: '4. 받는 사람 정보 입력',
        text: '받는 분의 주소 및 연락처, 그리고 은행 계좌 정보를 입력합니다.',
        src: 'img-step-4.svg',
      }, {
        title: '5. 송금 신청',
        text: '송금 신청 버튼을 누르고, 안내되는 모인 계좌로 송금 금액을 입력합니다.',
        src: 'img-step-5.svg',
      }, {
        title: '6. 송금 완료',
        text: '송금 내역에서 상황을 확인할 수 있으며, 보낸 돈이 도착하면 알림이 전송됩니다.',
        src: 'img-step-6.svg',
      },
    ];
  }

  render() {
    return (
    <div className='themoin-landing-step'>
      {LandingStep.ITEMS.map((item, index) => (
        <div key={ `step-${index}` }>
          <img src={ `${ IMG_URL }/${ item.src }` } />
          <div>{ item.title }</div>
          <p>{ item.text }</p>
        </div>
      ))}
    </div>
    );
  }
}


