import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { updateRemitRecipient } from '../action';
import {
  RecipientsUpdate,
  DEFAULT_RECIPIENT_CNY,
  DEFAULT_RECIPIENT_JPY,
} from './RecipientsUpdate';

export class RemitFormRecipientsAdd extends RecipientsUpdate {
  componentWillMount() {
    this.updateRecipient(this.props.component.unit);
  }

  componentWillReceiveProps(nextProps) {
    const vTokenGap = nextProps.vToken - this.props.vToken;
    if (vTokenGap > 0) {
      this.setState({
        vToken: this.state.vToken + vTokenGap,
      });
    }

    if (this.props.component.unit !== nextProps.component.unit) {
      this.updateRecipient(nextProps.component.unit);
    }
  }

  updateRecipient(unit) {
    if (!this.props.isUpdate) {
      const recipient = unit === 'CNY' ? DEFAULT_RECIPIENT_CNY : DEFAULT_RECIPIENT_JPY;
      recipient.isNew = true;

      this.props.dispatch(updateRemitRecipient(recipient));
      this.setState({unit, recipient});
    } else {
      const recipient = Object.assign({}, this.props.recipient);
      this.setState({unit, recipient});
    }
  }

  componentDidUpdate() {
    this.props.dispatch(updateRemitRecipient(this.state.recipient));
  }

  render() {
    return (
    <div className='themoin-remit-form-recipients-add'>
      <h2 style={{ textAlign: 'center' }}>받는 사람의 정보를 입력해 주세요.</h2>
      <p style={{ marginTop: '15px', textAlign: 'center' }}>
        기입한 연락처로 중요 안내 메일과 문자가 발송됩니다. 중요 안내를 받지 못하는 일이 없도록 정확히 입력해주세요.
      </p>
      { this.renderFormContents() }
    </div>
    );
  }
}

RemitFormRecipientsAdd.propTypes = {
  dispatch: PropTypes.func,
  component: PropTypes.object,
  view: PropTypes.string,
  recipients: PropTypes.array,
  recipientId: PropTypes.number,
  recipient: PropTypes.object,
  focusable: PropTypes.bool,
};

RemitFormRecipientsAdd.mapState = state => ({
  view: state.view,
  recipients: state.recipients,
  recipientId: state.recipientId,
  component: state.remit.component,
  recipient: state.remit.recipient,
});

export default connect(RemitFormRecipientsAdd.mapState)(RemitFormRecipientsAdd);



